import dayjs from 'dayjs';
import React, { FC, useEffect, useRef, useState } from 'react';
import { CalendarViewOption, FlexibleDateType, TimeConstraintConfig, TimePartExtended, TimePart } from '../types/HappinDateTime';

const DEFAULT_CONSTRAINTS: TimeConstraintConfig = {
	hours: {
		min: 0,
		max: 23,
		step: 1
	},
	minutes: {
		min: 0,
		max: 59,
		step: 1
	},
	seconds: {
		min: 0,
		max: 59,
		step: 1
	},
	milliseconds: {
		min: 0,
		max: 999,
		step: 1
	}
};

function pad( type: keyof TimeConstraintConfig, value: number ) {
	const padValues = {
		hours: 1,
		minutes: 2,
		seconds: 2,
		milliseconds: 3
	};

	let str = value.toString();
	while ( str.length < padValues[ type ] )
		str = '0' + str;
	return str;
}

interface Props {
    viewDate: dayjs.Dayjs;
    showView: ( view: CalendarViewOption, date: FlexibleDateType )=>void;
    timeFormat: string | true;
    selectedDate: dayjs.Dayjs;
	dateFormat?: string | false;
	setTime: (type: TimePart, value: number)=>void;
	timeConstraints: TimeConstraintConfig;
}

const TimeView: FC<Props> = ({
	viewDate,
	showView,
	timeFormat,
	dateFormat,
	selectedDate,
	setTime,
	timeConstraints
}) => {
	const [constraints, setConstraints] = useState(DEFAULT_CONSTRAINTS);

	const [timeParts, setTimeParts] = useState({} as Record<TimePartExtended, string>);

	const prevSelectedDate = useRef<dayjs.Dayjs>();

	useEffect(() => {
		const appliedConstraints = {} as typeof DEFAULT_CONSTRAINTS;

		(Object.keys( DEFAULT_CONSTRAINTS ) as (keyof TimeConstraintConfig)[]).forEach( type => {
			appliedConstraints[ type ] = { ...DEFAULT_CONSTRAINTS[type], ...(timeConstraints[type] || {}) };
		});

		setConstraints(appliedConstraints);
	}, [timeConstraints])

	useEffect(()=>{
		if ( prevSelectedDate.current && selectedDate?.isSame(prevSelectedDate.current) ) return;
		const date = (selectedDate || viewDate)
		setTimeParts({
			hours: pad( 'hours', date.hour() ),
			minutes: pad( 'minutes', date.minute() ),
			seconds: pad( 'seconds', date.second() ),
			milliseconds: pad('milliseconds', date.millisecond() ),
			ampm: date.hour() < 12 ? 'AM' : 'PM'
		})
		prevSelectedDate.current = selectedDate
	}, [selectedDate?.toISOString(), viewDate?.toISOString()])

	const counters = [] as TimePartExtended[];
	const format = timeFormat === true ? 'hh:mm A' : timeFormat

	if ( /h/i.test(format) ) {
		counters.push('hours');
		if ( /m/.test(format) ) {
			counters.push('minutes');
			if ( /s/.test(format) ) {
				counters.push('seconds');
				if ( /S/.test(format) ) {
					counters.push('milliseconds');
				}
			}
		}
		if ( /a/i.test(format) ) {
			counters.push('ampm');
		}
	}

	const items: React.ReactNode[] = [];

	
	function increase( type: TimePart ) {
		
		setTimeParts( t => {

			const tc = constraints[ type ];

			let value = parseInt( t[ type ], 10) + tc.step!;

			if ( value > tc.max! )

			value = tc.min! + ( value - ( tc.max! + 1 ) );
			return {
				...t,
				[type]: pad( type, value )
			}
		}) ;
	}

	function decrease( type: TimePart ) {
		setTimeParts( t => {
			const tc = constraints[ type ];
			let value = parseInt( timeParts[ type ], 10) - tc.step!;
			if ( value < tc.min! )
				value = tc.max! + 1 - ( tc.min! - value );

			return {
				...t,
				[type]: pad( type, value )
			}
		});
	}

	function _setTime(type: TimePart) {
		setTimeParts( t => {
			setTime( type, +t[ type ] );
			return t
		})
		
	}

	function onStartClicking( e: React.MouseEvent<HTMLSpanElement>, action: (type: TimePart)=>void, type: TimePartExtended ) {
		if ( e && e.button && e.button !== 0 ) {
			// Only left clicks, thanks
			return;
		}
		
		if ( type === 'ampm' ) {
			let intHours = +timeParts.hours!;
		
			if ( intHours >= 12 ) {
				intHours -= 12;
			} else {
				intHours += 12;
			}

			setTime( 'hours', intHours );

			return;
		}

		action( type ) 

		let increaseTimer: NodeJS.Timeout

		const timer = setTimeout( () => {
			increaseTimer = setInterval( () => {
				action( type )
			}, 70);
		}, 500);

		const mouseUpListener = () => {
			clearTimeout( timer );
			clearInterval( increaseTimer );
			_setTime( type );
			document.removeEventListener( 'mouseup', mouseUpListener );
			document.removeEventListener( 'touchend', mouseUpListener );
		};

		document.addEventListener( 'mouseup', mouseUpListener );
		document.addEventListener( 'touchend', mouseUpListener );
	}

	// function renderCounter( type: TimePartExtended, value: string | number ) {
	// 	if ( type === 'hours' && /a/i.test(format) ) {
	// 		value = ( +value - 1 ) % 12 + 1;

	// 		if ( +value === 0 ) {
	// 			value = 12;
	// 		}
	// 	}

	// 	if ( type === 'ampm' ) {
	// 		if ( /A/.test(format) ) {
	// 			value = viewDate.format('A');
	// 		}
	// 		else {
	// 			value = viewDate.format('a');
	// 		}
	// 	}

	// 	return (
			
	// 	);
	// }

	// counters.forEach( (c, i) => {
		
	// });

	return (
		<div className="rdtTime">
			<table>
				{ dateFormat && 
					<thead>
						<tr>
							<td className="rdtSwitch" colSpan={4} onClick={ () => showView('days', viewDate) }>
								{ (selectedDate || viewDate).format( dateFormat ) }
							</td>
						</tr>
					</thead>
				}
				<tbody>
					<tr>
						<td>
							<div className="rdtCounters">
								{ counters.map( (c, i) => (
									<>
										{ !!i && (c !== 'ampm') && <div key={ `sep${i}` } className="rdtCounterSeparator">:</div>}
										{
											<div key={ c } className="rdtCounter">
												<span className="rdtBtn" onMouseDown={ e => onStartClicking( e, increase, c)}></span>
												<div className="rdtCount">
													{ ( c === 'hours' && /a/i.test(format) )
														? (( +timeParts[c] - 1 ) % 12 + 1) || 12 
														: c === "ampm" 
															? dayjs().hour(+timeParts.hours).format(/A/.test(format) ? ' A' : ' a') 
															: timeParts[c] 
													}
												</div>
												<span className="rdtBtn" onMouseDown={ e => onStartClicking( e, decrease, c)}></span>
											</div>
										}
									</>
								)) }
							</div>
						</td>
					</tr>
				</tbody>
			</table>
		</div>
	);
}


export default TimeView

