////// Packages //////////////////
import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Link } from 'react-router-dom';
import { Card, CardBody } from 'reactstrap';

////// Local Components //////////

////// Global Components /////////

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////


const UnderConstruction = () => (
  <Card className="text-center">
    <CardBody className="p-5">
      <div className="display-1 text-200 fs-error">Working On It</div>
      <p className="lead mt-4 text-800 text-sans-serif font-weight-semi-bold">
        {`The page you're looking for is under construction.`}
      </p>
      <hr />
      <p>
        {`We are hard at work building the most intuitive business management app on the web`}
        <a href="mailto:info@happin.design" className="ml-1">
          We would love to hear from you. Contact Us
        </a>
        .
      </p>
      <Link className="btn btn-primary btn-sm mt-3" to="/">
        <FontAwesomeIcon icon="home" className="mr-2" />
        Take me home
      </Link>
    </CardBody>
  </Card>
);

export default UnderConstruction;
