import MonthsView from './views/MonthsView';
import YearsView from './views/YearsView';
import TimeView from './views/TimeView';
import { CalendarViewOption, FlexibleDateType, RenderDateFunction, RenderMonthFunction, RenderYearFunction, TimeConstraintConfig } from './types/HappinDateTime';
import classNames from 'classnames';
import onClickOutside from 'react-onclickoutside';

interface ClickOutProps extends React.HTMLProps<HTMLDivElement> {
	className: string;
	children: React.ReactNode;
	onClickOut: ( e: MouseEvent )=>void
}


