class ClickOutBase extends React.Component<ClickOutProps> {
	container = React.createRef<HTMLDivElement>();

	render() {
		return (
			<div className={ this.props.className } ref={ this.container }>
				{ this.props.children }
			</div>
		);
	}
	handleClickOutside(e: MouseEvent) {
		this.props.onClickOut( e );
	}

	setClickOutsideRef() {
		return this.container.current;
	}
}

const ClickableWrapper = onClickOutside( ClickOutBase );


const viewModes: Record<string, CalendarViewOption> = {
	YEARS: 'years',
	MONTHS: 'months',
	DAYS: 'days',
	TIME: 'time',
} as const;

const nofn = function () {};

export interface DateTimeProps {
	id?: string,
	value?: FlexibleDateType,
	initialValue?: FlexibleDateType,
	initialViewMode?: CalendarViewOption,
	onClose?: (date?: FlexibleDateType)=>void,
	onChange?: (date?: string)=>void,
	onNavigate?: (view: CalendarViewOption)=>void,
	onBeforeNavigate?: (view: CalendarViewOption, currentView: CalendarViewOption, date: FlexibleDateType)=>CalendarViewOption,
	onNavigateBack?: ( modifier: number, unit: dayjs.OpUnitType )=>void,
	onNavigateForward?: ( modifier: number, unit: dayjs.OpUnitType )=>void,
	updateOnView?: CalendarViewOption,
	locale?: string,
	iso?: boolean,
	dateFormat?: string | boolean,
	timeFormat?: string | boolean,
	timeConstraints?: TimeConstraintConfig,
	isValidDate?: (date?: dayjs.Dayjs | undefined) => boolean,
	open?: boolean,
	setOpen?: (open: boolean)=>void;
	closeOnSelect?: boolean,
	renderDay?: RenderDateFunction,
	renderMonth?: RenderMonthFunction,
	renderYear?: RenderYearFunction,
	className?: string,
	closeOnClickOutside?: boolean;
	staticDisplay?: boolean;
	dropLeft?: boolean;
}

const DateTime: FC<DateTimeProps> = ({
	id,
	value,
	initialValue,
	initialViewMode,
	onClose=nofn,
	onChange=nofn,
	onNavigate=nofn,
	onBeforeNavigate=(e:any)=>e,
	onNavigateBack=nofn,
	onNavigateForward=nofn,
	updateOnView,
	locale,
	iso,
	dateFormat,
	timeFormat,
	timeConstraints={},
	isValidDate=()=>true,
	open,
	setOpen,
	closeOnSelect,
	closeOnClickOutside=true,
	renderDay,
	renderMonth,
	renderYear,
	className='',
	staticDisplay,
	dropLeft
}) => {

	const [currentView, setCurrentView] = useState( initialViewMode || ( dateFormat ? 'days' : timeFormat ? 'time' : 'years' ) );
	const [viewDate, setViewDate] = useState( dayjs(value || initialValue) );
	const [updateView, setUpdateView] = useState('time' as CalendarViewOption)
	const [selectedDate, setSelectedDate] = useState( dayjs(value || initialValue) );

	const [outputFormat, setOutputFormat] = useState('');

	useEffect( () => {
		if (!value) return
		const d = dayjs(value)
		if (d.isValid()) setViewDate(d)
	}, [value] )
	
	useEffect(() => {

		const dateForm = dateFormat === true ? 'L' : dateFormat;
		const timeForm = timeFormat === true ? 'LT' : timeFormat;

		setOutputFormat( dateForm ? (timeForm ? dateForm + ' ' + timeForm : dateForm) : (timeForm ? timeForm : ''))

	}, [timeFormat, dateFormat]);

	useEffect(() => {

		const d = dateFormat ? (dateFormat === true ? 'L' : dateFormat) : ''

		if ( updateOnView ) {
			setUpdateView(updateOnView)

		} else if ( d.match(/[lLD]/) ) {
			setUpdateView(viewModes.DAYS)

		} else if ( d.indexOf('M') !== -1 ) {
			setUpdateView(viewModes.MONTHS)

		} else if ( d.indexOf('Y') !== -1 ) {
			setUpdateView(viewModes.YEARS)

		} else { 
			setUpdateView(viewModes.TIME) 

		}

	}, [dateFormat]);

	useEffect(()=>{
		const d = dayjs(value);
		if ( d.isValid() ) {
			setSelectedDate( d )
		} else {
			setSelectedDate( dayjs() )
		}
		
	}, [value])

	const _showView = ( view: CalendarViewOption, date: FlexibleDateType ) => {
		const d = date || viewDate;
		const nextView = onBeforeNavigate( view, currentView, d );
		if ( nextView && currentView !== nextView ) {
			onNavigate( nextView );
			setCurrentView( nextView );
		}
	}

	function _onChange(value?: dayjs.Dayjs) {
		if (closeOnSelect) _closeCalendar();
		if (iso) {
			onChange( value?.toISOString() )
		} else {
			onChange( value?.format( outputFormat ) )
		}
	}

	const _viewNavigate = ( modifier: number, unit: dayjs.OpUnitType ) => {

		// Subtracting is just adding negative time
		setViewDate(viewDate => viewDate.add( modifier, unit ) );

		if ( modifier > 0 ) {
			onNavigateForward( modifier, unit );
		} else {
			onNavigateBack( -(modifier), unit );
		}

	}

	const _setTime = ( type: 'hours' | 'minutes' | 'seconds' | 'milliseconds', value: number ) => {
		const date = selectedDate || viewDate;
		let newDate: dayjs.Dayjs;

		switch (type) {
			case 'hours':
				newDate = date.hour( value )
				break;
			case 'minutes':
				newDate = date.minute( value )
				break;
			case 'seconds':
				newDate = date.second( value )
				break;
			case 'milliseconds':
				newDate = date.millisecond( value )
				break;
		}

		setSelectedDate(newDate)
		setViewDate(newDate)
		_onChange( newDate )

	}

	const _closeCalendar = () => { 
		if (setOpen) setOpen(false) 
		onClose(selectedDate)
	}
	
	const _handleClickOutside = () => {
		if ( open && closeOnClickOutside ) {
			_closeCalendar();
		}
	}

	return (
		<ClickableWrapper 
			className={classNames('rdt', className, {
				'rdtStatic': !!staticDisplay,
				'rdtOpen': !!open
			})} 
			onClickOut={ _handleClickOutside }
		>
			{/* { input && 
				renderInput 
					? (
						<div> 
							{ renderInput( inputProps, _openCalendar, _closeCalendar ) }
						</div>
					)
					: <input { ...inputProps } />
			} */}
			<div id={id} className="rdtPicker rounded bg-light" style={dropLeft ? {right: 0} : undefined}>
				{ currentView === 'years' && 
					<YearsView 
						viewDate={ viewDate }
						renderYear={ renderYear }
						isValidDate={ isValidDate }
						navigate={ _viewNavigate }
						showView={ _showView }
						selectedDate={ selectedDate }
						updateDate={ e => {
							const updatedDate = dayjs(viewDate)
												.year(+e.currentTarget.getAttribute('data-value')!)
							setViewDate(updatedDate)
							if (updateView === viewModes.YEARS) {
								setSelectedDate(updatedDate)
								_onChange(updatedDate)
							} else {
								_showView( viewModes.MONTHS, viewDate )
							}
						}  }
					/>
				}
				{ currentView === 'months' && 
					<MonthsView 
						viewDate={ viewDate }
						renderMonth={ renderMonth }
						isValidDate={ isValidDate }
						navigate={ _viewNavigate }
						showView={ _showView }
						selectedDate={ selectedDate }
						updateDate={ e => {
							const updatedDate = dayjs(viewDate)
												.year(+e.currentTarget.getAttribute('data-year')!)
												.month(+e.currentTarget.getAttribute('data-value')!)
							setViewDate(updatedDate)
							if (updateView === viewModes.MONTHS) {
								setSelectedDate(updatedDate)
								_onChange(updatedDate)
							} else {
								_showView( viewModes.DAYS, viewDate )
							}
						} }
					/>
				}
				{ currentView === 'days' && 
					<DaysView 
						renderDay={ renderDay }
						isValidDate={ isValidDate }
						navigate={ _viewNavigate }
						showView={ _showView }
						timeFormat={ timeFormat }
						viewDate={ viewDate }
						selectedDate={ selectedDate }
						updateDate={ e => {
							const updatedDate = dayjs(viewDate)
												.year(+e.currentTarget.getAttribute('data-year')!)
												.month(+e.currentTarget.getAttribute('data-month')!)
												.date(+e.currentTarget.getAttribute('data-value')!)
							setViewDate(updatedDate)
							if (updateView === viewModes.DAYS) {
								setSelectedDate(updatedDate)
								_onChange(updatedDate)
							} else if ( timeFormat ) {
								_showView( viewModes.HOURS, viewDate )
							}
						} }
					/>
				}
				{ currentView === 'time' && timeFormat && 
					<TimeView 
						viewDate={ viewDate }
						showView={ _showView }
						timeFormat={ timeFormat }
						dateFormat={ dateFormat === true ? 'L' : dateFormat }
						selectedDate={ selectedDate }
						setTime={ _setTime }
						timeConstraints={ timeConstraints }
					/>
				}
			</div>
		</ClickableWrapper>
	)
}

export default DateTime


