////// Packages //////////////////
import React, { FC, useState } from 'react'
import { Card, CardBody, Col, Row } from 'reactstrap'

////// Local Components //////////

////// Global Components /////////
import DisplayHighlight from 'components/DisplayHighlight'
import FalconCardHeader from 'components/FalconCardHeader'
import { MPerson } from 'store/types/models/id'
import useLeaveBanks from 'store/hooks/time/useLeaveBanks'
import { chunk } from 'lodash'
import useTimeCodes from 'store/hooks/time/useTimeCodes'
import { FalconColors } from 'components/falconTypes'

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Types /////////////////////

/* const colors = ['info', 'warning', 'primary', 'success', 'dark'] as FalconColors[] */

interface Props {
    personId: MPerson['_id'];
    className?: string;
}

const DisplayAccrual: FC<Props> = ({personId, className}) => {
    const { value: leaveBanks, ...loader} = useLeaveBanks(personId)
    const { value: timeTypes, ...typesLoader } = useTimeCodes()

    return (
        <Card className={className}>
            <FalconCardHeader title="Accruals" />
            <CardBody>
                {(!!leaveBanks && leaveBanks.length === 4 ? chunk(leaveBanks, 2) : chunk(leaveBanks, 3)).map( (row, index) => {
                    return (
                        <Row noGutters key={index}>
                            {row.map( (bank, indx) => {
                                const currentAccrual = bank.accruals && bank.accruals.length > 0 && bank.accruals[bank.accruals.length - 1]
                                const timeOff = (currentAccrual && currentAccrual.value || 0) - ( bank.newChargedHours || 0 )
                                const timeCode = timeTypes?.find( type => type.timeCodes.find( code => code._id === bank.timeCodeId ) )
                                return (
                                    <Col sm xl={12} key={indx} >
                                        <DisplayHighlight 
                                            info={timeOff}
                                            label={`${bank.title || timeCode?.title || 'No Description'} ${bank.unit.charAt(0).toUpperCase() + bank.unit.slice(1)}${timeOff === 1 ? '': 's'}`} 
                                        />
                                    </Col>
                                )
                            })}
                        </Row>
                    )
                })}
                    
            </CardBody>
        </Card>
    )
}

export default DisplayAccrual