import { BaseDuck } from "store/ducks/BaseDuck";
import { ConfigurationBank } from "store/ducks/forms/configureBanks";
import { timeTypes as typesDuck, timeEntries as entriesDuck, timeOffBanks as timeOffBanksDuck, timeOffRequests as timeOffRequestsDuck } from "store/ducks/time";
import { DateType } from "store/typedef";
import { ActionCreator } from "store/types/interface/common";
import { MPerson } from "store/types/models/id";
import { MTimeBank, MTimeCode, MTimeEntry, MTimeType, MTimeOffRequest } from "store/types/models/time";
import { MAssignment, MTask } from "store/types/models/workflow";

interface TimeTypeController extends BaseDuck<MTimeType> {
    getTimeTypes: ActionCreator<['getTimeTypes']>;
    createTimeType: ActionCreator<['createTimeType']>;
    createTimeCode: ActionCreator<['createTimeCode']>;
    deleteTimeType: ActionCreator<['deleteTimeType']>;
    updateTimeType: ActionCreator<['updateTimeType']>;
}

interface TimeEntryController extends BaseDuck<MTimeEntry> {
    getTimeEntries: ActionCreator<['getTimeEntries']>;
    createTimeEntry: ActionCreator<['createTimeEntry']>;
    updateTimeEntry: ActionCreator<['updateTimeEntry']>;
    reviewTimeEntry: ActionCreator<['reviewTimeEntry']>;
    deleteTimeEntry: ActionCreator<['deleteTimeEntry']>;
}

interface TimeOffRequestsController extends BaseDuck<MTimeOffRequest> {
    createTimeOffRequest: ActionCreator<['createTimeOffRequest']>;
    getTimeOffRequests: ActionCreator<['getTimeOffRequests']>;
    approveTimeOffRequest: ActionCreator<['approveTimeOffRequest']>;
    updateTimeOffRequest: ActionCreator<['updateTimeOffRequest']>;
    deleteTimeOffRequest: ActionCreator<['deleteTimeOffRequest']>;
}

interface TimeOffBanksController extends BaseDuck<MTimeBank> {
    createTimeBank: ActionCreator<['createTimeBank']>
    updateTimeBank: ActionCreator<['updateTimeBank']>
    deleteTimeBank: ActionCreator<['deleteTimeBank']>
    getLeaveBanks: ActionCreator<['getTimeBanks']>
}

export const timeTypes = typesDuck as TimeTypeController
export const timeEntries = entriesDuck as TimeEntryController
export const timeOffBanks = timeOffBanksDuck as TimeOffBanksController
export const timeOffRequests = timeOffRequestsDuck as TimeOffRequestsController

// TIME TYPES AND CODES

export const getTimeTypes: ActionCreator<['getTimeTypes']> = () => {
    const actions = [{
        name: 'getTimeTypes',
        data: {

        }
    }] as const;
    return { actions }
}

timeTypes.getTimeTypes = getTimeTypes;

export const createTimeType: ActionCreator<['createTimeType']> = (title: string, eventColor: MTimeType['eventColor'], isWorkflow?: boolean) => {
    const actions = [{
        name: 'createTimeType',
        data: {
            title,
            eventColor,
            isWorkflow
        }
    }] as const;
    return {actions}
}

timeTypes.createTimeType = createTimeType

export const createTimeCode: ActionCreator<['createTimeCode']> = (title: string, timeTypeId: MTimeType['_id'], bankable?: boolean) => {
    const actions = [{
        name: 'createTimeCode',
        data: {
            title,
            bankable: !!bankable,
            timeTypeId
        }
    }] as const;
    return {actions}
}
timeTypes.createTimeCode = createTimeCode

export const deleteTimeType: ActionCreator<['deleteTimeType']> = (timeTypeId: MTimeType['_id']) => {
    const actions = [{
        name: 'deleteTimeType',
        data: {
            timeTypeId
        }
    }] as const;
    return {actions}
}

timeTypes.deleteTimeType = deleteTimeType

interface TimeTypeUpdate {
    title: MTimeType['title'], 
    eventColor: MTimeType['eventColor'], 
    isWorkflow: MTimeType['isWorkflow']
}

export const updateTimeType: ActionCreator<['updateTimeType']> = (timeTypeId: MTimeType['_id'], update: TimeTypeUpdate) => {
    const actions = [{
        name: 'updateTimeType',
        data: {
            timeTypeId,
            ...update
        }
    }] as const;
    return {actions}
}
timeTypes.updateTimeType = updateTimeType


//ENTRIES

export const getTimeEntries: ActionCreator<['getTimeEntries']> = (personId: MPerson['_id'], startDate: string, endDate: string) => {
    const actions = [{
        name: 'getTimeEntries',
        data: {
            personId,
            startDate,
            endDate
        }
    }] as const;
    return {actions}
}

timeEntries.getTimeEntries = getTimeEntries;

interface NewTimeEntryProps {
    personId: MPerson['_id'],
    startTime: string,
    endTime: string,
    timeCodeId: MTimeCode['_id'],
    comments?: string,
    taskId?: MTask['_id'],
    assignmentId?: MAssignment['_id']
}

export const createTimeEntry: ActionCreator<['createTimeEntry']> = (timeEntry: NewTimeEntryProps) => {
    const actions = [{
        name: 'createTimeEntry',
        data: timeEntry
    }] as const;
    return {actions}
}

timeEntries.createTimeEntry = createTimeEntry;

interface TimeEntryUpdate {
    timeCodeId?: MTimeCode['_id'];
    startTime?: string;
    endTime?: string;
    comments?: string;
    taskId?: MTask['_id'];
    assignmentId?: MAssignment['_id']
}

export const updateTimeEntry: ActionCreator<['updateTimeEntry']> = (timeEntryId: MTimeEntry['_id'], update: TimeEntryUpdate) => {
    const actions = [{
        name: 'updateTimeEntry',
        data: {
            timeEntryId,
            ...update
        }
    }] as const;
    return {actions}
}
timeEntries.updateTimeEntry = updateTimeEntry

export const deleteTimeEntry: ActionCreator<['deleteTimeEntry']> = (timeEntryId: MTimeEntry['_id']) => {
    const actions = [{
        name: 'deleteTimeEntry',
        data: {
            timeEntryId
        }
    }] as const;
    return {actions}
}
timeEntries.deleteTimeEntry = deleteTimeEntry

export const reviewTimeEntry: ActionCreator<['reviewTimeEntry']> = (timeEntryId: MTimeEntry['_id'], approved: boolean, declinedReason?: string) => {
    const actions = [{
        name: 'reviewTimeEntry',
        data: {
            timeEntryId,
            approved,
            declinedReason
        }
    }] as const;
    return {actions}
}

timeEntries.reviewTimeEntry = reviewTimeEntry

// TIME OFF

interface TimeOffRequestOptions {
    startDate: string;
    endDate: string;
    title: string;
    allDay?: boolean;
    description?: string;
    timeCodeId?: MTimeCode['_id'];
    absentHours?: number;
}

export const createTimeOffRequest: ActionCreator<['createTimeOffRequest']> = (request: TimeOffRequestOptions) => {
    const actions = [{
        name: 'createTimeOffRequest',
        data: request
    }] as const;
    return { actions }
}

timeOffRequests.createTimeOffRequest = createTimeOffRequest



export const updateTimeOffRequest: ActionCreator<['updateTimeOffRequest']> = (requestId: MTimeOffRequest['_id'], update: Partial<TimeOffRequestOptions>) => {
    const actions = [{
        name: 'updateTimeOffRequest',
        data: {
            ...update,
            requestId
        }
    }] as const;
    return { actions }
}

timeOffRequests.updateTimeOffRequest = updateTimeOffRequest


export const deleteTimeOffRequest: ActionCreator<['deleteTimeOffRequest']> = (requestId: MTimeOffRequest['_id']) => {
    const actions = [{
        name: 'deleteTimeOffRequest',
        data: {
            requestId
        }
    }] as const;
    return { actions }
}

timeOffRequests.deleteTimeOffRequest = deleteTimeOffRequest


export const getTimeOffRequests: ActionCreator<['getTimeOffRequests']> = (personId?: MPerson['_id'], startDate?: DateType, endDate?: DateType) => {
    const actions = [{
        name: 'getTimeOffRequests',
        data: {
            personId,
            startDate,
            endDate
        }
    }] as const;
    return { actions }
}

timeOffRequests.getTimeOffRequests = getTimeOffRequests

// Can both approve of decline, depending on value of the "approve" parameter
export const approveTimeOffRequest: ActionCreator<['approveTimeOffRequest']> = (requestId: MTimeOffRequest['_id'], approve: boolean, declineReason?: string) => {
    const actions = [{
        name: 'approveTimeOffRequest',
        data: {
            requestId,
            approve: approve || undefined,
            decline: !approve || undefined,
            declineReason
        }
    }] as const;
    return { actions }
}

timeOffRequests.approveTimeOffRequest = approveTimeOffRequest


export const getLeaveBanks: ActionCreator<['getTimeBanks']> = (personId: MPerson['_id']) => {
    const actions = [{
        name: 'getTimeBanks',
        data: {
            personId
        }
    }] as const;
    return { actions }
}

timeOffBanks.getLeaveBanks = getLeaveBanks

export const createTimeBank: ActionCreator<['createTimeBank']> = (bank: ConfigurationBank) => {
    const actions = [{
        name: 'createTimeBank',
        data: {
            title: bank.title,
            personId: bank.personId,
            timeCodeId: bank.timeCodeId,
            unit: bank.unit,
            hoursPerUnit: +bank.hoursPerUnit,
            policy: {
                accrualPeriod: bank.policy.accrualPeriod,
                accrualAmount: +bank.policy.accrualAmount,
                accrueWeeks: bank.policy.accrueWeeks,
                accrueWeekdays: bank.policy.accrueWeekdays,
                accrueDates: bank.policy.accrueDates,
                accrueMonths: bank.policy.accrueMonths,
                accrualMaximum: bank.policy.accrualMaximum ? +bank.policy.accrualMaximum : undefined,
                effectiveDate: bank.policy.effectiveDate,
                autoAccrual: bank.policy.autoAccrual
            },
            templateTitle: bank.templateTitle
        }
    }] as const;
    return { actions }
}

timeOffBanks.createTimeBank = createTimeBank

export const updateTimeBank: ActionCreator<['updateTimeBank']> = (configuration: Partial<ConfigurationBank> & {timeBankId: MTimeBank['_id']}) => {
    const actions = [{
        name: 'updateTimeBank',
        data: {
            ...configuration
        }
    }] as const;
    return { actions }
}

timeOffBanks.updateTimeBank = updateTimeBank

export const deleteTimeBank: ActionCreator<['deleteTimeBank']> = (timeBankId: MTimeBank['_id']) => {
    const actions = [{
        name: 'deleteTimeBank',
        data: {
            timeBankId
        }
    }] as const;
    return { actions }
}

timeOffBanks.deleteTimeBank = deleteTimeBank

