import React, { FC } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import { RouteComponentProps } from 'react-router'
import UnderConstruction from 'layouts/errors/UnderConstruction';
import Timesheet from 'layouts/time/timesheet/Timesheet';
import TimeOff from 'layouts/time/timeoff/TimeOff';

interface Props extends RouteComponentProps {

}

const TimeRoutes: FC<Props> = ({ match: { url } }) => (
  <Switch>
    <Route path={`${url}/timesheet`} component={Timesheet} />
    <Route path={`${url}/timeoff`} exact component={TimeOff} />
    <Route path={`${url}/approvals`} exact component={UnderConstruction} /> 

    {/*Redirect*/}
    <Redirect to="/errors/404" />
  </Switch>
);

export default TimeRoutes;
