import { shallowEqual } from "react-redux";
import { getTimeTypes } from "store/thunks/time";
import useLoadAndSelect from "../useLoadAndSelect";

const useTimeCodes = () => {
    return useLoadAndSelect({
        actionCreator: getTimeTypes,
        selector: state => Object.values(state.time.timeTypes),
        equality: shallowEqual,
        deduplicate: ['TimeCodes']
    })
}

export default useTimeCodes;