////// Packages //////////////////
import { DateSelectArg, EventClickArg } from '@fullcalendar/common';
import React, { FC, useState } from 'react'
import { Col, Row } from 'reactstrap';
import useTimeCodes from 'store/hooks/time/useTimeCodes';





////// Local Components //////////
import AddScheduleModal from './AddScheduleModal';
import CalendarEventModal, { CalendarEventModalEvent } from '../components/CalendarEventModal';

////// Global Components /////////
import DisplayAccrual from '../components/DisplayAccrual';
import Calendar from '../Calendar';
import { useParams } from 'react-router';
import { useSelector } from 'react-redux';
import useTimeOffRequests from 'store/hooks/time/useTimeOffRequests';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Types /////////////////////



interface Props {
    
}

const TimeOff: FC<Props> = () => {
    const {personId} = useParams<{personId?: string}>()
    const myPersonId = useSelector( state => state.app.login.personId )
    const selectedPersonId = personId || myPersonId
    const {value: timeOffRequests, ...timeOffRequestLoader } = useTimeOffRequests(selectedPersonId)

    const [isOpenScheduleModal, setIsOpenScheduleModal] = useState(false);
    const [scheduleInitialStartDate, setScheduleInitialStartDate] = useState('');
    const [scheduleInitialEndDate, setScheduleInitialEndDate] = useState('');
    const [isOpenEventModal, setIsOpenEventModal] = useState(false);
    const [eventModalEventData, setEventModalEventData] = useState<CalendarEventModalEvent>();

    function onDateSelect(arg: DateSelectArg | null) {
        setIsOpenScheduleModal(true)
        setScheduleInitialStartDate(arg?.start?.toISOString() || '')
        setScheduleInitialEndDate(arg?.end?.toISOString() || '')
    }

    function onEventClick(arg: EventClickArg) {
        setIsOpenEventModal(true)
        if (arg.event) {
            const {
                _id,
                description,
                badgeLabel,
                badgeColor,
            } = arg.event.extendedProps
            setEventModalEventData({
                ...arg.event.extendedProps,
                _id,
                title: arg.event.title,
                start: arg.event.start,
                end: arg.event.end,
                allDay: arg.event.allDay,
                description: description,
                badge: {
                    label: badgeLabel,
                    color: badgeColor
                },
                entryType: 'timeOff'
            })
        }
        
    }

    const events = timeOffRequests?.map( req => ({
        ...req,
        start: new Date(req.startDate),
        end: new Date(req.endDate)
    }))
    return (
        <>
            <Row className="mt-3">
                <Col xl={3} className="order-0 order-xl-1">
                    {selectedPersonId && <DisplayAccrual personId={selectedPersonId}/>}
                </Col>
                <Col xl={9} className="order-1 order-xl-0">
                    <Calendar 
                        events={events}
                        onDateSelect={onDateSelect}
                        onEventClick={onEventClick}
                        addEventText="New Time Off Request"
                    />
                </Col>
            </Row>
            <AddScheduleModal
                initialStartDate={scheduleInitialStartDate}
                initialEndDate={scheduleInitialEndDate}
                isOpen={isOpenScheduleModal}
                setIsOpen={setIsOpenScheduleModal}
            />
            {
                eventModalEventData && 
                    <CalendarEventModal
                        isOpen={isOpenEventModal}
                        setIsOpen={setIsOpenEventModal}
                        event={eventModalEventData}
                        personId={selectedPersonId!}
                    />
            }
        </>
    )
}

export default TimeOff;