////// Packages //////////////////
import React, { useState, useEffect, FC } from 'react';
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  FormGroup,
  Label,
  CustomInput,
  Row,
  Col
} from 'reactstrap';

////// Local Components //////////

////// Global Components /////////
import Flex from 'components/Flex';
import FormGroupInput from 'components/FormGroupInput';
import FormGroupSelect from 'components/FormGroupSelect';
import DateTimeInputGroup from 'components/datetime/DateTimeInputGroup';
import useAction from 'store/hooks/useAction';
import { toast } from 'react-toastify';
import { createTimeOffRequest, updateTimeOffRequest } from 'store/thunks/time';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////
import { MTimeOffRequest, MTimeCode } from 'store/types/models/time'
import useTimeCodes from 'store/hooks/time/useTimeCodes';

const labelOptions = [
  {value: 'none', label: "None"},
  {value: 'business', className: "bg-soft-info", label: 'Business'},
  {value: 'important', className: "bg-soft-danger", label: 'Important'},
  {value: 'personal', className: "bg-soft-warning", label: 'Personal'},
  {value: 'mustattend', className: "bg-soft-success", label: 'Must Attend'}
]

interface Props {
  isOpen: boolean;
  setIsOpen: React.Dispatch<React.SetStateAction<boolean>>;
  initialStartDate?: string;
  initialEndDate?: string;
  event?: {
    _id: MTimeOffRequest['_id'];
    title: string;
    allDay?: boolean;
    description?: string;
    absentHours?: number;
    timeCodeId?: string;
  }
}

const AddScheduleModal: FC<Props> = ({
  isOpen,
  setIsOpen,
  initialStartDate="",
  initialEndDate="",
  event
}) => {

  const addTimeOffAction = useAction( createTimeOffRequest );
  const updateTimeOffAction = useAction( updateTimeOffRequest );
  const {value: timeTypes} = useTimeCodes()

  const [endDate, setEndDate] = useState<string>();
  const [startDate, setStartDate] = useState<string>();
  const [title, setTitle] = useState<string>();
  const [allDay, setAllDay] = useState(false);
  const [description, setDescription] = useState<string>();
  const [absentHours, setAbsentHours] = useState<number>();
  const [code, setCode] = useState<string>();

  const closeBtn = (
    <button className="close font-weight-normal" onClick={handleClose}>
      &times;
    </button>
  );

  useEffect(() => {
    if (event) {
      setTitle(event.title);
      setAllDay(!!event.allDay);
      setDescription(event.description);
      setAbsentHours(event.absentHours);
      setCode(event.timeCodeId)
    }
  }, [event])

  useEffect(() => { setStartDate(initialStartDate) }, [initialStartDate]);
  useEffect(() => { setEndDate(initialEndDate) }, [initialEndDate]);

  function handleClose() {
    setIsOpen(false);
    setStartDate(undefined);
    setEndDate(undefined);
  }

  async function handleSubmit() {
    if (event?._id) {
      const data = await updateTimeOffAction.post(event._id, {startDate, endDate, title, allDay, description, absentHours, timeCodeId: code})
      if (data?.success) {
        toast.success('Updated time off request')
        handleClose()
      } else {
        toast.error(data?.error || 'Something went wrong')
      }
    } else {
      const data = await addTimeOffAction.post({startDate, endDate, title, allDay, description, timeCodeId: code, absentHours})
      if (data?.success) {
        toast.success('Submitted time off request')
        handleClose()
      } else {
        toast.error(data?.error || 'Something went wrong')
      }
    }
    
  }

  const bankableCodes = timeTypes
                          ?.map( type => ({
                              ...type,
                              label: type.title,
                              options: type.timeCodes
                                              .filter( code => code.bankable )
                                              .map( code => ({
                                                  ...code,
                                                  label: code.title,
                                                  value: code._id
                                              }))
                          }))
                          .filter( type => type.options.length > 0 )

  return (
    <Modal isOpen={isOpen} toggle={handleClose} modalClassName="theme-modal" contentClassName="border">
      <ModalHeader toggle={handleClose} className="bg-light d-flex flex-between-center border-bottom-0" close={closeBtn}>
        New Leave Request
      </ModalHeader>
      <ModalBody>
        <FormGroupInput
          id="new-time-off-title"
          label="Title"
          placeholder="Title..."
          name="title"
          required
          value={title}
          onChange={e=>setTitle(e.target.value)}
        />
        <FormGroup>
          <Label className="fs-0" for="new-time-off-start">
            Start Date
          </Label>
          <DateTimeInputGroup
            id="new-time-off-start"
            value={startDate}
            onChange={dateTime => setStartDate(dateTime)}
            dateFormat="MM/DD/YYYY"
            timeFormat={allDay ? false : undefined}
            pickerProps={{
              isValidDate: endDate ? (date => !!date?.isBefore(endDate)) : undefined
            }}
            placeholder={allDay ? 'MM/DD/YYYY' : 'MM/DD/YYYY h:mm AM/PM'}
          />
        </FormGroup>
        <FormGroup>
          <Label className="fs-0" for="new-time-off-end">
            End Date
          </Label>
          <DateTimeInputGroup
            id="new-time-off-end"
            value={endDate}
            onChange={dateTime => setEndDate(dateTime)}
            dateFormat="MM/DD/YYYY"
            timeFormat={allDay ? false : undefined}
            pickerProps={{
              isValidDate: startDate ? (date => !!date?.isAfter(startDate)) : undefined
            }}
            placeholder={allDay ? 'MM/DD/YYYY' : 'MM/DD/YYYY h:mm AM/PM'}
          />
        </FormGroup>
        <FormGroup>
          <CustomInput
            name="new-time-off-all-day"
            type="checkbox"
            id="allDay"
            label="All Day"
            checked={allDay}
            onChange={e => setAllDay(!!e.target.checked)}
          />
        </FormGroup>
        
        <FormGroupInput
          id="new-time-off-description"
          label="Description"
          type="textarea"
          name="description"
          value={description}
          onChange={e => setDescription(e.target.value)}
        />
        <Row>
          <Col md>
            <FormGroupInput
              id="new-time-off-hours"
              type="number"
              label="Absent Hours"
              name="hours"
              value={absentHours}
              onChange={e => {
                if (!isNaN(+e.target.value) )
                  return setAbsentHours(+e.target.value)

                if (!e.target.value && +e.target.value !== 0) 
                  setAbsentHours(undefined)
              }}
            />
          </Col>
          <Col md>
            <FormGroupSelect
              id="new-time-off-label"
              label="Type"
              name="type"
              value={ bankableCodes?.find( type => type.options.find( option => option.value === code ) )?.options.find( option => option.value === code )}
              formatGroupLabel={ group => <div className={`p-2 badge-${group.eventColor} rounded`}>{group.label}</div>}
              options={ bankableCodes }
              onChange={option => setCode(option?.value)}
            />
          </Col>
        </Row>
      </ModalBody>
      <ModalFooter tag={Flex} justify="end" align="center" className="bg-light border-top-0">
        <Button color="primary" onClick={handleSubmit} className="px-4">
          Save
        </Button>
      </ModalFooter>
    </Modal>
  );
};

export default AddScheduleModal;
