////// Packages //////////////////
import React, { FC, useEffect, useRef, useState } from 'react';
import { Button, Card, CardBody, CardHeader, Col, Row, UncontrolledTooltip } from 'reactstrap';
import FullCalendar, { CalendarApi, DateSelectArg, EventClickArg, EventInput, EventSource } from '@fullcalendar/react';
import dayGridPlugin from '@fullcalendar/daygrid';
import bootstrapPlugin from '@fullcalendar/bootstrap';
import timeGridPlugin from '@fullcalendar/timegrid';
import listPlugin from '@fullcalendar/list';
import interactionPlugin from '@fullcalendar/interaction';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

////// Local Components //////////

////// Global Components /////////
import DropdownFilter from 'components/DropdownFilter';
import Flex from 'components/Flex';
import dayjs from 'dayjs';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////


//// SETTINGS ////
const buttonText = {
  today: 'Today',
  month: 'Month view',
  week: 'week',
  day: 'day',
  listWeek: 'list view',
  listYear: 'year'
};

const eventTimeFormat = {
  hour: 'numeric',
  minute: '2-digit',
  omitZeroMinute: true,
  meridiem: true
} as const;

const viewName = [
  'Month View', 
  'Week View', 
  'Day View', 
  'List View', 
  'Year View'
];

type ViewOption = 'Month View' | 
                  'Week View' | 
                  'Day View' | 
                  'List View' | 
                  'Year View'

const viewMap = {
  'Month View': 'dayGridMonth', 
  'Week View': 'timeGridWeek', 
  'Day View': 'timeGridDay', 
  'List View': 'listWeek', 
  'Year View': 'listYear'
} as const;

interface Props {
  events?: EventInput[];
  addEventText?: string;
  onEventClick?: (e: EventClickArg)=>void;
  onDateSelect?: (info: DateSelectArg | null)=>void;
  defaultView?: ViewOption
}

const Calendar: FC<Props> = ({events=[], addEventText, onEventClick, onDateSelect, defaultView='Month View'}) => {
  const calendarRef = useRef<FullCalendar>(null);
  const [calendarApi, setCalendarApi] = useState<CalendarApi>();
  const [title, setTitle] = useState<string>();
  const [currentFilter, setCurrentFilter] = useState(defaultView as string);
  const [viewAllTime, setViewAllTime] = useState(false);

  const views = {
    week: {
      eventLimit: 5,
      slotMinTime: viewAllTime ? "00:00:00" : "07:00:00",
      slotMaxTime: viewAllTime ? "24:00:00" : "19:00:00"
    },
    day: {
      eventLimit: 5,
      slotMinTime: viewAllTime ? "00:00:00" : "07:00:00",
      slotMaxTime: viewAllTime ? "24:00:00" : "19:00:00"
    }
  };

  useEffect(() => {
    setCalendarApi(calendarRef.current?.getApi());
  }, []);

  const handleFilter = (filter: ViewOption) => {
    setCurrentFilter(filter);
    if (!calendarApi) return
    calendarApi.changeView(viewMap[filter] || 'listYear');
    setTitle(calendarApi?.getCurrentData().viewTitle);
  };

  const countOutOfRange = events.reduce( (total, event) => {
                                              if(!(event.start && event.end)) return total
                                              const start = dayjs(event.start)
                                              const end = dayjs(event.end)
                                              if (start.add(1, 'day').isBefore(end)) return total
                                              if (start.hour() > 7 && start.hour() < 18) return total
                                              if (end.hour() > 7 && end.hour() < 18) return total
                                              return total + 1
                                          }, 0)
  // const displayEvents = !(currentFilter === 'Day View' || currentFilter === 'WeekView') || viewAllTime ? events : events.slice(0)
  //                               .sort((a, b) => new Date(a.start as Date).getTime() - new Date(b.start as Date).getTime())
  //                               .reduce( (display, event) => {
  //                                 const start = dayjs(event.start)
  //                                 const end = dayjs(event.end)
  //                                 console.log(start.hour(), end.hour())
  //                                 if (!(start.hour() < 7 || start.hour() > 18) || !(end.hour() < 7 || end.hour() > 18) || start.add(1, 'day').isBefore(end)) {
  //                                   return [...display, event]
  //                                 }
  //                                 const lastEvent = display[display.length - 1]
  //                                 const lastEnd = lastEvent && dayjs(lastEvent.end)
  //                                 if (lastEvent?.endDay) {
  //                                   if (start.hour() < 7) {
  //                                     return [...display, {
  //                                       ...event,
  //                                       start: start.hour(7).minute(0).toDate(),
  //                                       end: end.hour(7).minute(30).toDate(),
  //                                       startDay: true,
  //                                       count: 1
  //                                     }]
  //                                   }
  //                                   if (lastEnd?.add(1, 'day').isAfter(end)) {
  //                                     return [...display.slice(0, -1), {...lastEvent, count: lastEvent.count + 1}]
  //                                   }
  //                                   return [...display, {
  //                                     ...event,
  //                                     start: start.hour(18).minute(30).toDate(),
  //                                     end: end.hour(19).minute(0).toDate(),
  //                                     endDay: true,
  //                                     count: 1,
  //                                     title: '+ 1 more'
  //                                   }]
  //                                 } else if (lastEvent?.startDay && lastEnd?.isAfter(end)) {
                                      
  //                                     return [...display.slice(0, -1), {...lastEvent, count: lastEvent.count + 1}]
  //                                 } else {
  //                                   if (start.hour() > 19) {
  //                                     return [...display, {
  //                                       ...event,
  //                                       start: start.hour(18).minute(30).toDate(),
  //                                       end: end.hour(19).minute(0).toDate(),
  //                                       endDay: true,
  //                                       count: 1,
  //                                       title: '+ 1 more'
  //                                     }]
  //                                   }
  //                                   return [...display, {
  //                                     ...event,
  //                                     start: start.hour(7).minute(0).toDate(),
  //                                     end: end.hour(7).minute(30).toDate(),
  //                                     startDay: true,
  //                                     count: 1
  //                                   }]
  //                                 }
  //                               }, [] as EventInput[])

  // console.log(displayEvents)
  return (
    <>
      <Card className="mb-3">
        <CardHeader>
          <Row noGutters className="align-items-center">
            <Col xs="auto" className="d-flex justify-content-end order-md-1">
              <UncontrolledTooltip placement="bottom" target="previous">
                Previous
              </UncontrolledTooltip>
              <UncontrolledTooltip placement="bottom" target="next">
                Next
              </UncontrolledTooltip>
              <Button
                onClick={() => {
                  calendarApi?.prev();
                  setTitle(calendarApi?.getCurrentData().viewTitle);
                }}
                color="link"
                className="icon-item icon-item-sm icon-item-hover shadow-none p-0 mr-1 ml-md-2"
                id="previous"
              >
                <FontAwesomeIcon icon="arrow-left" />
              </Button>
              <Button
                onClick={() => {
                  calendarApi?.next();
                  setTitle(calendarApi?.getCurrentData().viewTitle);
                }}
                color="link"
                className="icon-item icon-item-sm icon-item-hover shadow-none p-0 mr-1"
                id="next"
              >
                <FontAwesomeIcon icon="arrow-right" />
              </Button>
            </Col>
            <Col xs="auto" md="auto" className="order-md-2">
              <h4 className="mb-0 fs-0 fs-sm-1 fs-lg-2 calendar-title">
                {title || `${calendarApi?.getCurrentData().viewTitle}`}
              </h4>
            </Col>
            <Col xs md="auto" tag={Flex} justify="end" className="order-md-3">
              <Button
                size="sm"
                color="falcon-primary"
                onClick={() => {
                  calendarApi?.today();
                  setTitle(calendarApi?.getCurrentData().viewTitle);
                }}
              >
                Today
              </Button>
            </Col>
            <Col md="auto" className="d-md-none">
              <hr />
            </Col>
            {
              onDateSelect && addEventText && 
                <Col xs="auto" className="d-flex order-md-0">
                  <Button color="primary" size="sm" onClick={() => onDateSelect(null)}>
                    <FontAwesomeIcon icon="plus" className="mr-1" />{addEventText}
                  </Button>
                </Col>
            }
            
            <Col className="d-flex justify-content-end order-md-2">
              <DropdownFilter
                className="mr-2"
                filters={viewName}
                currentFilter={currentFilter}
                handleFilter={handleFilter}
                icon="sort"
                right
              />
            </Col>
          </Row>
        </CardHeader>
        <CardBody className="p-0">
          <FullCalendar
            ref={calendarRef}
            headerToolbar={false}
            plugins={[dayGridPlugin, bootstrapPlugin, timeGridPlugin, interactionPlugin, listPlugin]}
            initialView={viewMap[defaultView]}
            themeSystem="bootstrap"
            dayMaxEvents={2}
            height={'auto'}
            stickyHeaderDates={false}
            editable
            selectable
            selectMirror
            select={onDateSelect}
            views={views}
            eventTimeFormat={eventTimeFormat}
            eventClick={onEventClick}
            events={events}
            buttonText={buttonText}
          />
          {
            (currentFilter === 'Day View' || currentFilter === 'Week View') && 
            <>
              {
                countOutOfRange && !viewAllTime &&
                  <Flex align="center" justify="center">
                    <h5 className="my-2">{`+ ${countOutOfRange} hidden`}</h5>
                  </Flex>
              }
              <Flex column>
                <Button color="falcon-primary" id="calendar-expand" onClick={()=>setViewAllTime(v=>!v)}>
                  <FontAwesomeIcon icon="angle-down"  transform={viewAllTime ? "rotate-180" : ''}/>
                </Button>
                <UncontrolledTooltip target="calendar-expand">{viewAllTime ? "Collapse Time" : "Show All Time"}</UncontrolledTooltip>
              </Flex>
            </>
          }
        </CardBody>
      </Card>
    </>
  );
};

export default Calendar;
