////// Packages //////////////////
import React, { FC } from 'react'

////// Local Components //////////

////// Global Components /////////
import { FalconColors } from './falconTypes'

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Types /////////////////////

interface Props {
    info: React.ReactNode;
    color?: FalconColors;
    label: React.ReactNode;
    className?: string;
}

const DisplayHighlight: FC<Props> = ({info, color='black', label, className}) => {
    
    
    return (
        <div className={className}>
            <div className="d-flex justify-content-center">
                <h1 className={"text-" + color + " text-center"}>
                    {info}
                </h1>
            </div>
            <div className="d-flex justify-content-center">
                <h6 className="text-center">{label}</h6>
            </div>
        </div>
    )
}

export default DisplayHighlight;