////// Packages //////////////////
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { FalconColors } from 'components/falconTypes';
import dayjs from 'dayjs';
import React, { FC, useEffect, useState } from 'react'
import { Button, Input, InputGroup, InputGroupAddon, InputProps } from 'reactstrap'
import HappinDateTime, {DateTimeProps} from './HappinDateTime';

////// Local Components //////////

////// Global Components /////////

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Types /////////////////////

interface Props extends Omit<InputProps, 'onChange'> {
    onChange?: (date: string)=>void;
    pickerProps?: Omit<DateTimeProps, 'setter' | 'value'>;
    buttonColor?: FalconColors;
    autoFillBlank?: boolean
}

const DateTimeInputGroup: FC<Props> = ({dateFormat="MM/DD/YY", timeFormat="hh:mm A", value, onChange, onBlur, pickerProps, size, autoFillBlank, buttonColor="primary", ...inputProps}) => {
    const [dateValue, setDateValue] = useState('');
    const [isOpen, setIsOpen] = useState(false);
    const [firstRender, setFirstRender] = useState(true)
    const format = (dateFormat && timeFormat) ? (dateFormat + ' ' + timeFormat) : ( dateFormat || timeFormat )

    useEffect(()=> {
        if (firstRender) {
            setFirstRender(false);
            if (!value && !autoFillBlank) return;
        }
        
        const d = dayjs(value)
        if ( d.isValid() ) {
            setDateValue( d.format( format ) )
        } else {
            setDateValue( value )
        }
        
    }, [value])

    function handleInputBlur() {
        const d = dayjs(dateValue);
        if ( d.isValid() ) {
            if (onChange) onChange( d.format( format ) )
        } else {
            if (onChange) onChange( dateValue )
        }

    }
    return (
        <div>
            <InputGroup>
                <Input 
                    onChange={e=>setDateValue(e.target.value)} 
                    onBlur={ handleInputBlur } 
                    value={dateValue}
                    size={size}
                    {...inputProps} 
                />
                <InputGroupAddon addonType="append">
                    <Button
                        outline
                        color={buttonColor}
                        onClick={()=>setIsOpen(o=>!o)}
                        size={size}
                    >
                        <FontAwesomeIcon icon='calendar' />
                    </Button>
                </InputGroupAddon>
                
            </InputGroup>
            <HappinDateTime
                        dateFormat={dateFormat}
                        timeFormat={timeFormat}
                        onChange={date => {
                            const d = dayjs(date)
                            if ( d.isValid() ) {
                                setDateValue( d.format(format) )
                                if (onChange) onChange( d.format(format) )
                            }
                        }}
                        value={dateValue}
                        dropLeft
                        open={isOpen}
                        onClose={(date)=>{
                            setIsOpen(false);
                            if(pickerProps?.onClose) pickerProps.onClose(date);
                        }}
                        {...pickerProps}
                    />
                
        </div>

    )
}

export default DateTimeInputGroup

