import React, { FC } from 'react';
import { DropdownItem, DropdownItemProps } from 'reactstrap';
import Flex from 'components/Flex';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome/index';
import classNames from 'classnames';

interface Props extends DropdownItemProps {
  filter: string;
  currentFilter: string;
  children: React.ReactNode;
  className?: string;
}
const DropdownItemFilter: FC<Props> = ({ filter, currentFilter, className, children, ...rest }) => (
  <DropdownItem tag={Flex} justify="between" className={classNames('cursor-pointer', className)} {...rest}>
    {children}
    {filter === currentFilter && <FontAwesomeIcon icon="check" transform="down-4 shrink-4" />}
  </DropdownItem>
)

export default DropdownItemFilter;
