////// Packages //////////////////
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import dayjs from 'dayjs';
import React, { FC, useEffect, useState } from 'react'
import { toast } from 'react-toastify';
import { Button, Col, FormGroup, Label, Modal, ModalBody, ModalHeader, Row } from 'reactstrap';

////// Local Components //////////

////// Global Components /////////
import FormGroupInput from 'components/FormGroupInput';
import FormGroupSelect from 'components/FormGroupSelect';
import DateTimeInputGroup from 'components/datetime/DateTimeInputGroup';

////// CommonJS Helpers //////////
import { pluralizeIf } from 'helpers/pluralizeIf';

////// Custom Hooks //////////////
import useTimeCodes from 'store/hooks/time/useTimeCodes';
import useAction from 'store/hooks/useAction';

////// Thunks ////////////////////
import { getCondensedTimesheetInfo } from 'store/thunks/workflow/stages';
import { createTimeEntry } from 'store/thunks/time';
import { updateTimeEntry } from 'store/thunks/time'

////// Types /////////////////////
import { MPerson } from 'store/types/models/id';
import { MAssignment, MStage, MTask, StageTimeInfo, StageTimeInfoTask } from 'store/types/models/workflow';
import { MTimeEntry, MTimeType, MTimeCode } from 'store/types/models/time';

type BaseOption<T extends Record<string, any> = Record<string, unknown>> = {value: string, label: string} & {[K in keyof T]: T[K]} | null | undefined

type AssignmentOption = BaseOption<{taskId?: string, stageId?: string}>
type CategoryOption = BaseOption<{ color?: string, isWorkflow?: boolean }>
type CodeOption = BaseOption<{ bankable?: boolean }>

export const DT_FORMAT = 'YYYY-MM-DDTHH:mm'

export interface TimeEntryModalEvent {
    _id: MTimeEntry['_id'];
    comments?: MTimeEntry['comments']
    timeTypeId: MTimeType['_id'];
    timeCodeId: MTimeCode['_id'];
    taskId?: MTask["_id"];
    taskTitle?: MTask['title'];
    stageId?: MStage['_id'];
    assignment?: {
        _id: MAssignment['_id'];
        description?: MAssignment['description'];
        assignedHours?: MAssignment['assignedHours'];
    }
}

interface Props {
    selectedPersonId?: MPerson["_id"];
    startDate?: string;
    endDate?: string;
    setIsOpen: React.Dispatch<React.SetStateAction<boolean>>;
    isOpen: boolean;
    event?: TimeEntryModalEvent
}

const TimeEntry: FC<Props> = ({selectedPersonId, startDate, endDate, setIsOpen, isOpen, event}) => {
    const timesheetInfo = useAction( getCondensedTimesheetInfo );
    const newEntryAction = useAction( createTimeEntry );
    const updateEntryAction = useAction( updateTimeEntry );

    const [selectedStartDate, setSelectedStartDate] = useState<string>()
    const [selectedEndDate, setSelectedEndDate] = useState<string>()
    const [selectedAssignment, setSelectedAssignment] = useState(undefined as AssignmentOption);
    const [assignmentOptions, setAssignmentOptions] = useState([] as AssignmentOption[]);
    const [selectedCategory, setSelectedCategory] = useState(undefined as CategoryOption);
    const [selectedCode, setSelectedCode] = useState(undefined as CodeOption);
    const [selectedTask, setSelectedTask] = useState(undefined as BaseOption);
    const [selectedStage, setSelectedStage] = useState(undefined as BaseOption);
    const [selectedMilestone, setSelectedMilestone] = useState(undefined as BaseOption);
    const [newEntryDescription, setNewEntryDescription] = useState('');
    const [stageData, setStageData] = useState( null as StageTimeInfo[] | null);

    const { value: timeCodes, ...timeCodesLoader } = useTimeCodes();

    async function getInfo() {
        const data = await timesheetInfo.post(selectedPersonId, false /* true for active only */)
        if (data?.success) {
            // @ts-ignore
            setStageData(data.records[0].stageData as StageTimeInfo[])
        }
    }
    
    useEffect(()=>{
        if (startDate) setSelectedStartDate(startDate)
    }, [startDate])

    useEffect(()=>{
        if (endDate) setSelectedEndDate(endDate)
    }, [endDate])

    useEffect(() => {
        if (selectedPersonId) getInfo()
    }, [selectedPersonId])

    const stage = selectedStage && stageData?.find( stage => stage._id === selectedStage.value );
    const milestone = selectedMilestone && stage?.milestones?.find( m => m._id === selectedMilestone.value )
    const taskOptions = milestone?.tasks.map( taskId => stage?.tasks.find(t => t._id === taskId) ).filter( (s): s is StageTimeInfoTask => !!s ).map(task => ({value: task._id, label: task.title }))

    function handleSelectAssignment(option: AssignmentOption) {
        const o = option
        setSelectedAssignment( o );
        if (!o) return;
        const stage = stageData?.find( stage => stage._id === o.stageId )
        setSelectedStage( stage ? { value: stage._id, label: `${stage.type}: ${stage.identifier} - ${stage.title}` } : undefined)
        const milestone = stage?.milestones.find( m => m.tasks.includes( o.taskId! ) )
        setSelectedMilestone( milestone ? {value: milestone._id, label: milestone.title} : undefined )
        const task = stage?.tasks.find( task => task._id === o.taskId )
        setSelectedTask( task ? {value: task._id, label: task.title} : undefined )

    }


    function synchronizeDatesFromStart(newStartDate: string) {
        const dayjsStart = dayjs(newStartDate)
        if ( dayjsStart.isValid() ) {
            const dayjsEnd = dayjs(selectedEndDate)
            if (!selectedEndDate || !dayjsEnd.isValid() || dayjsEnd.isBefore(dayjsStart)) {
                setSelectedEndDate( dayjsStart.add(1, 'hour').format(DT_FORMAT) )
            }
        } else {
            console.log('invalid', newStartDate)
        }
    }

    function synchronizeDatesFromEnd(newEndDate: string) {
        console.log('synchronize from end')
        const dayjsEnd = dayjs(newEndDate)
        if ( dayjsEnd.isValid() ) {
            const dayjsStart = dayjs( selectedStartDate )
            if (!selectedStartDate || !dayjsStart.isValid() || dayjsEnd.isBefore(dayjsStart)) {
                setSelectedStartDate( dayjsEnd.subtract(1, 'hour').format(DT_FORMAT) )
            }
        } else {
            console.log('invalid', newEndDate)
        }
    }

    const startDateInvalid = !selectedStartDate || !dayjs(selectedStartDate).isValid() || (selectedEndDate && !dayjs(selectedStartDate).isBefore(selectedEndDate))
    const endDateInvalid = !selectedEndDate || !dayjs(selectedEndDate).isValid() || (selectedStartDate && !dayjs(selectedEndDate).isAfter(selectedStartDate))

    const isValidEntry = selectedPersonId 
                            && selectedStartDate 
                            && selectedEndDate
                            && selectedCode && selectedCode.value
                            && dayjs(selectedStartDate).isValid()
                            && dayjs(selectedEndDate).isValid()

    async function handleSubmitNewTimeEntry() {
        if (!isValidEntry) {
            toast.error('Entry is invalid')
            return;
        }
        
        if (event?._id) {
            const entry = {
                startTime: dayjs(selectedStartDate).toISOString(),
                endTime: dayjs(selectedEndDate).toISOString(),
                timeCodeId: selectedCode!.value,
                comments: newEntryDescription || undefined,
                taskId: selectedTask?.value,
                assignmentId: selectedAssignment?.value
            }
            const data = await updateEntryAction.post(event._id, entry)
            if (data?.success) {
                toast.success("Added time entry");
                setIsOpen(false);
                setSelectedStartDate('');
                setSelectedEndDate('');
                setNewEntryDescription('');
            } else {
                toast.error(data?.error || 'Something went wrong')
            }
        } else {
            const entry = {
                personId: selectedPersonId,
                startTime: dayjs(selectedStartDate).toISOString(),
                endTime: dayjs(selectedEndDate).toISOString(),
                timeCodeId: selectedCode!.value,
                comments: newEntryDescription || undefined,
                taskId: selectedTask?.value,
                assignmentId: selectedAssignment?.value
            }
            const data = await newEntryAction.post(entry)
            if (data?.success) {
                toast.success("Added time entry");
                setIsOpen(false);
                setSelectedStartDate('');
                setSelectedEndDate('');
                setNewEntryDescription('');
            } else {
                toast.error(data?.error || 'Something went wrong')
            }
        }
        
    }
    
    useEffect(() => {
        if (!stageData) {
            setAssignmentOptions([])
            return
        }
        const assignments = []
        for (const stage of stageData) {
            for (const task of stage.tasks) {
                for (const assignment of task.assignments) {
                    assignments.push({
                        taskId: task._id,
                        stageId: stage._id,
                        value: assignment._id,
                        label: `${task.title} (${assignment.assignedHours} hr${assignment.assignedHours === 1 ? '' : 's'})`
                    })
                }
            }
        }
        setAssignmentOptions( assignments );
        if (event) {
            const eventAssignment = event.assignment ? {
                taskId: event.taskId,
                stageId: event.stageId,
                value: event.assignment._id,
                label: `${event.taskTitle} (${event.assignment.assignedHours} ${pluralizeIf("hr", event.assignment.assignedHours)})`
            } : undefined
    
            setSelectedAssignment( eventAssignment )

            const stage = stageData?.find( stage => stage._id === event.stageId );
            const milestone = stage?.milestones?.find( m => !!stage?.milestones.find( m => m.tasks.includes(event.taskId) ) )
            const task = stage?.tasks.find(t => t._id === event.taskId)
            const taskOption = task ? {value: task._id, label: task.title } : undefined
            setSelectedStage(stage ? {
                    value: stage._id, 
                    label: `${stage.type}: ${stage.identifier} - ${stage.title}` 
                } : undefined
            )
            setSelectedMilestone(milestone ? {
                    value: milestone._id, 
                    label: milestone.title
                } : undefined
            )
            setSelectedTask(taskOption)
        }
    }, [stageData, event])
    
    useEffect(()=> {
        if (timeCodes && event) {
            const category = timeCodes.find( type => type._id === event.timeTypeId)
            setSelectedCategory(category ? {
                value: category._id,
                label: category.title,
                isWorkflow: category.isWorkflow,
                color: category.eventColor
            } : undefined)
            const timeCode = category?.timeCodes.find( tc => tc._id === event.timeCodeId )
            setSelectedCode(timeCode ? {
                value: timeCode._id,
                label: timeCode.title,
                bankable: timeCode.bankable
            } : undefined)
            setNewEntryDescription( event?.comments || '')
        }
    }, [event, ...(timeCodes || [])])
    
    return (
        <Modal size="lg" isOpen={isOpen} toggle={()=>setIsOpen(o=>!o)}>
                <ModalHeader className="bg-light border-bottom-0">
                    New Time Entry
                </ModalHeader>
                <ModalBody>
                   <Row>
                        <Col lg>
                            <FormGroup>
                                <Label for='new-time-entry-start-time'>Start Time</Label>
                                <DateTimeInputGroup 
                                    buttonColor="info" 
                                    onChange={ val => {
                                        setSelectedStartDate(val);
                                        synchronizeDatesFromStart(val);
                                    }} 
                                    value={selectedStartDate}
                                    invalid={startDateInvalid}
                                    pickerProps={{
                                        isValidDate: date => !selectedEndDate || !!date?.isBefore(selectedEndDate),
                                        timeConstraints: {
                                            minutes: {
                                                step: 5
                                            }
                                        }
                                    }}
                                />
                            </FormGroup>
                        </Col>
                        <Col lg>
                            <FormGroup>
                                <Label for='new-time-entry-end-time'>End Time</Label>
                                <DateTimeInputGroup 
                                    buttonColor="info" 
                                    onChange={ val => {
                                        setSelectedEndDate(val);
                                        synchronizeDatesFromEnd(val);
                                    }} 
                                    value={selectedEndDate}
                                    invalid={endDateInvalid}
                                    pickerProps={{
                                        isValidDate: date => !selectedStartDate || !!date?.isAfter(selectedStartDate),
                                        timeConstraints: {
                                            minutes: {
                                                step: 5
                                            }
                                        }
                                    }}
                                />
                            </FormGroup>
                        </Col>
                    </Row>
                    <Row>
                        <Col lg>
                            <FormGroupSelect 
                                id="new-time-type"
                                label="Category"
                                placeholder="Select time category..."
                                value={ selectedCategory }
                                options={ timeCodes?.map( tc => ({value: tc._id, label: tc.title, color: tc.eventColor, isWorkflow: tc.isWorkflow })) }
                                formatOptionLabel={ option => <div className={`badge-${option.color} p-2 rounded w-100`}>{option.label}</div>}
                                onChange={ option=>{ 
                                    setSelectedCategory(option); 
                                    setSelectedCode(null); 
                                } }
                                isLoading={ timeCodesLoader.loading }
                                isClearable
                                // select from timeCodes
                            />
                        </Col>
                        <Col lg>
                            <FormGroupSelect 
                                id="new-time-code"
                                label="Time Code"
                                placeholder="Select time code..."
                                value={ selectedCode }
                                options={ timeCodes?.find( tc => tc._id === selectedCategory?.value )?.timeCodes.map( tc => ({value: tc._id, label: tc.title, bankable: tc.bankable })) }
                                formatOptionLabel={ option => <>{option.bankable && <FontAwesomeIcon icon="archive" className="text-success"/>}{option.label}</> }
                                onChange={ setSelectedCode }
                                isLoading={ timeCodesLoader.loading }
                                isClearable
                                // select from timeCodes.timeCodes
                            />
                        </Col>
                    </Row>
                    { selectedCategory?.isWorkflow &&
                        <div className="mx-2 px-3 border border-top-0 border-bottom-0">
                            <FormGroupSelect 
                                id="new-time-entry-assignment"
                                label="Assignment"
                                placeholder="Select from assignments..."
                                value={ selectedAssignment }
                                options={ assignmentOptions }
                                onChange={ handleSelectAssignment }
                                isLoading={ timesheetInfo.loading }
                                isClearable
                                // select from assignments
                            />
                            <FormGroupSelect 
                                id="new-time-entry-stage"
                                label="Stage"
                                placeholder="Select workflow stage..."
                                value={ selectedStage }
                                options={ stageData?.map( stage => ({value: stage._id, label: `${stage.type}: ${stage.identifier} - ${stage.title}` })) }
                                onChange={ option => {
                                    setSelectedStage(option) 
                                    setSelectedMilestone(undefined)
                                    setSelectedTask(undefined)
                                    setSelectedAssignment(undefined)
                                }}
                                isLoading={ timesheetInfo.loading }
                                isClearable
                                // select from assignments
                            />
                            <FormGroupSelect 
                                id="new-time-entry-milestone"
                                label="Milestone"
                                placeholder="Select milestone..."
                                value={ selectedMilestone }
                                options={ stage?.milestones.map( milestone => ({value: milestone._id, label: milestone.title })) }
                                onChange={ option => {
                                    setSelectedMilestone(option)
                                    setSelectedTask(undefined)
                                    setSelectedAssignment(undefined)
                                }}
                                isLoading={ timesheetInfo.loading }
                                isClearable
                                // select from assignments
                            />
                            <FormGroupSelect 
                                id="new-time-entry-task"
                                label="Task"
                                placeholder="Select task..."
                                value={ selectedTask }
                                options={ taskOptions }
                                onChange={ option => {
                                    setSelectedTask(option) 
                                    setSelectedAssignment(undefined)
                                }}
                                isLoading={ timesheetInfo.loading }
                                isClearable
                                // select from assignments
                            />
                        </div>
                    }
                    <FormGroupInput
                        id="new-entry-comments"
                        label="Comments"
                        type="textarea"
                        rows={3}
                        value={newEntryDescription}
                        onChange={e=>setNewEntryDescription(e.target.value)}
                    />
                    <Row>
                        <Col xs="auto" className="ml-auto">
                            <Button color="primary" disabled={!isValidEntry} onClick={handleSubmitNewTimeEntry}>Submit</Button>
                        </Col>
                    </Row>
                </ModalBody>
            </Modal>
    )
}

export default TimeEntry