import { getTimeEntries } from "store/thunks/time";
import { MPerson } from "store/types/models/id";
import useLoadAndSelect from "../useLoadAndSelect";

export const useTimeEntries =  (personId: MPerson['_id'], startDate: string, endDate: string) => {
    return useLoadAndSelect({
        actionCreator: getTimeEntries,
        postParams: [personId, startDate, endDate],
        selector: state => Object.values( state.time.timeEntries )
                        .filter( timeEntry => (timeEntry.personId === personId) 
                                                && (timeEntry.endTime > startDate) 
                                                && (timeEntry.startTime < endDate)
                        ),
        deduplicate: ['timeEntries', personId, startDate, endDate]
    })
}

export default useTimeEntries;