import React, {FC} from 'react';
import { DropdownMenu, DropdownToggle, DropdownToggleProps, UncontrolledDropdown } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome/index';
import { IconProp } from '@fortawesome/fontawesome-svg-core'
import { isIterableArray } from 'helpers/utils';
import DropdownItemFilter from './DropdownItemFilter';


interface Props<T> extends DropdownToggleProps {
  filters: T[];
  handleFilter: (filter: T) => void;
  currentFilter: string;
  icon: IconProp;
  right?: boolean;

}
const DropdownFilter: FC<Props> = ({ filters, handleFilter, currentFilter, icon, right, ...rest }) => (
  <UncontrolledDropdown>
    <DropdownToggle size="sm" color="falcon-default" {...rest}>
      {currentFilter && <span className="mr-2">{currentFilter}</span>}
      <FontAwesomeIcon icon={icon} />
    </DropdownToggle>
    <DropdownMenu className="border py-2" right={right}>
      {isIterableArray(filters) &&
        filters.map((filter, index) => (
          <DropdownItemFilter
            currentFilter={currentFilter}
            onClick={() => {
              handleFilter(filter);
            }}
            filter={filter}
            className="text-capitalize"
            key={index}
          >
            {filter}
          </DropdownItemFilter>
        ))}
    </DropdownMenu>
  </UncontrolledDropdown>
);

export default DropdownFilter;
