import dayjs from "dayjs";
import { shallowEqual } from "react-redux";
import { getTimeOffRequests } from "store/thunks/time";
import { MPerson } from "store/types/models/id";
import { MTimeOffRequest } from "store/types/models/time";
import useLoadAndSelect from "../useLoadAndSelect";

function useTimeOffRequests(personId?: MPerson['_id'], startDate?: string, endDate?: string) {
    return useLoadAndSelect<MTimeOffRequest[]>({
        actionCreator: personId ? getTimeOffRequests : undefined,
        postParams: personId,
        selector: state => Object.values(state.time.timeOffRequests).filter( request => 
                                                                                request.personId === personId 
                                                                                && (!startDate || dayjs(request.endDate || request.startDate ).isAfter(startDate))
                                                                                && (!endDate || dayjs( request.startDate ).isBefore(endDate))
                                                                            ),
        equality: shallowEqual,
        deduplicate: ['getTimeOffRequests', personId, startDate || 'nostart', endDate || 'noend']
    })
}

export default useTimeOffRequests;