import React, { FC } from 'react';

interface Props {
    onClickPrev: (event: React.MouseEvent<HTMLTableHeaderCellElement>)=>void;
    onClickSwitch: (event: React.MouseEvent<HTMLTableHeaderCellElement>)=>void;
    onClickNext: (event: React.MouseEvent<HTMLTableHeaderCellElement>)=>void;
    switchContent: React.ReactNode;
    switchColSpan?: number;
    switchProps?: React.DetailedHTMLProps<React.HTMLAttributes<HTMLTableHeaderCellElement>, HTMLTableHeaderCellElement> & {[extendedProp: string]: any};
}

const ViewNavigation: FC<Props> = ( { onClickPrev, onClickSwitch, onClickNext, switchContent, switchColSpan, switchProps } ) => {
	return (
		<tr>
			<th className="rdtPrev" onClick={ onClickPrev }>
				<span></span>
			</th>
			<th className="rdtSwitch" colSpan={ switchColSpan } onClick={ onClickSwitch } {...switchProps}>
				{ switchContent }
			</th>
			<th className="rdtNext" onClick={ onClickNext }>
				<span></span>
			</th>
		</tr>
	);
}

export default ViewNavigation;