import { isEqual } from "lodash";
import { shallowEqual } from "react-redux";
import { getLeaveBanks } from "store/thunks/time";
import { MPerson } from "store/types/models/id";
import { MTimeBankWithCharged } from "store/types/models/time";
import useLoadAndSelect from "../useLoadAndSelect";

function useLeaveBanks(personId: MPerson['_id']) {
    return useLoadAndSelect<MTimeBankWithCharged[]>({
        actionCreator: getLeaveBanks,
        postParams: personId,
        selector: state => Object.values(state.time.timeOffBanks).filter( bank => bank.personId === personId ),
        equality: isEqual,
        deduplicate: ['getLeaveBanks', personId]
    })
}

export default useLeaveBanks;