import React, { FC } from 'react';
import { FormGroup, Input, InputProps, Label, LabelProps, FormFeedback } from 'reactstrap';

interface Props extends InputProps {
  label?: string;
  formClass?: string;
  feedback?: React.ReactNode;
  isTooltipFeedback?: boolean;
  labelProps?: LabelProps;
}
const FormGroupInput: FC<Props> = ({ label, formClass='', feedback, valid, invalid, isTooltipFeedback, labelProps, ...rest }) => {
  const {id, ...inputProps} = rest;
  return (
    <FormGroup className={formClass}>
      {label && <Label htmlFor={id} {...labelProps} >{label}</Label>}
      <Input id={id} valid={valid} invalid={invalid} {...inputProps} />
      {feedback && <FormFeedback valid={valid} invalid={invalid} tooltip={isTooltipFeedback}>{feedback}</FormFeedback>}
    </FormGroup>
  )
};

export default FormGroupInput;