import React, {FC} from 'react';
import {  FormGroup, Label, LabelProps } from 'reactstrap';
import Select, {Props as SelectProps } from 'react-select';

export type FormGroupSelectOptions = { value: (string | number | readonly string[] | undefined), label: React.ReactNode }[]

interface Props extends SelectProps<any, false> {
  id: string;
  label: string;
  labelProps?: LabelProps
}

const FormGroupSelect: FC<Props> = ({ id, label, styles, labelProps, ...inputProps }) => (
  <FormGroup>
    {label && <Label for={id} {...labelProps}>{label}</Label>}
      <Select 
        classNamePrefix="react-select" 
        id={id} 
        styles={{
          ...styles,
          menu: provided => ({...provided, zIndex: 999})
        }}
        {...inputProps} />
      {/* //   {options && isIterableArray(options) &&
      //     options.map(({ value, label }, index) => (
      //       <option value={value} key={index} dangerouslySetInnerHTML={createMarkup(label)} />
      //     ))}
      // </Select> */}
  </FormGroup>
);

export default FormGroupSelect;
