////// Packages ////////////////

////// Ducks ///////////////////
import { BaseDuck } from "store/ducks/BaseDuck";
import { 
    stages as stagesDuck
} from "store/ducks/workflow";
import { FormState as StageForm } from "store/ducks/forms/addStage";

///// Helpers //////////////////////
import uploadDoc from 'helpers/uploadDoc';
import base64toBlob from "helpers/base64toBlob";

////// Thunk ///////////////////////

////// Types ///////////////////////
import { MStage } from "store/types/models/workflow";
import { ActionCreator } from "store/types/interface/common";
import { MOrganization, MPerson } from "store/types/models/id";
import { DropzoneFile } from "store/store";
import { DateType } from "store/typedef";



////////////////////////////////////////////////////////////////////////////////////////////////
//
//  extends the workflows duck to include thunk actions. workflows should be imported from here
//
////////////////////////////////////////////////////////////////////////////////////////////////

interface StageController extends BaseDuck<MStage> {
    create: ActionCreator<['createStage']>;
    getStageIdentifiers: ActionCreator<['getStageIdentifiers']>;
    createNewTag: ActionCreator<['createStageTag']>;
    createNewType: ActionCreator<['createStageType']>;
    getOne: ActionCreator<['getStageDetails']>;
    addStageMember: ActionCreator<['addStageMember']>;
    addStageOrganization: ActionCreator<['addStageOrganization']>;
    postNote: ActionCreator<['createNote']>;
    addStageDocuments: ActionCreator<['addStageDocuments']>;
    addStagePhotos: ActionCreator<['addStagePhotos']>;
    updateStage: ActionCreator<['updateStage']>;
    getCondensedTimesheetInfo: ActionCreator<['timesheetCondensedData']>;
}

export const stages = stagesDuck as StageController

export const create: ActionCreator<['createStage']> = async (stageInfo: StageForm) => {

        const arrDocuments = Object.values(stageInfo.documents);
        const arrDocumentsPosted = [] as {location: string; thumb?: {location: string}; title: string}[]
        const arrPhotosPosted = [] as {location: string, title: string, tags?: string[]}[]

        for await (const file of arrDocuments) {
            let docType: 'document' | 'photo' = 'document'
            if (/\.png$|\.jpg$|\.gif$|\.tiff$|\.jpeg/i.test(file.path)) {
                docType = 'photo'
            }
            const docData = await uploadDoc(base64toBlob(file.base64), docType, file.path)
            if (docData?.success && docData.files?.[docType]) {
                if (docType === 'photo') {
                    arrPhotosPosted.push({location: docData.files?.[docType], title: file.path })
                } else if (docType == 'document') {
                    arrDocumentsPosted.push({location: docData.files?.[docType].name, thumb: (docData.files?.[docType].thumb ? {location: docData.files?.[docType].thumb} : undefined), title: file.path })
                }
            }
        }
        const formData = {
            ...stageInfo,
            documents: arrDocumentsPosted,
            photos: arrPhotosPosted
        }
        const actions = [
            {
                name: 'createStage',
                data: formData
            }
        ] as const;
        return {actions}
    }
stages.create = create;

