export const addStageDocuments: ActionCreator<['addStageDocuments']> = async ( attachments: DropzoneFile[], stageId: MStage['_id']) => {

    const arrDocumentsPosted = [] as {location: string, thumb?: {location: string}, title: string}[]

    for ( const attachment of attachments ) {        
        const newDoc = await uploadDoc(base64toBlob(attachment.base64), 'document', attachment.path)
        if (newDoc?.success && newDoc.files?.document) {
            arrDocumentsPosted.push(
                {
                    location: newDoc.files?.document.name, 
                    thumb: newDoc.files?.document.thumb 
                                ? {location: newDoc.files?.document.thumb} 
                                : undefined, 
                    title: attachment.path
                }
            )
        }
    }

    if (!attachments || !attachments.length) return { cancel: true }
  
    const actions = [{
        name: 'addStageDocuments',
        data: {
            documents: arrDocumentsPosted,
            stageId
        }
    }] as const;
    return {actions}
}

stages.addStageDocuments = addStageDocuments;

export const addStagePhotos: ActionCreator<['addStagePhotos']> = async ( attachments: DropzoneFile[], stageId: MStage['_id'] ) => {

    const arrPhotosPosted = [] as {location: string, title: string}[]

    for ( const attachment of attachments ) {
        const newPhoto = await uploadDoc(base64toBlob(attachment.base64), 'photo', attachment.path)
        if (newPhoto?.success && newPhoto.files?.photo) {
            arrPhotosPosted.push(
                {
                    location: newPhoto.files?.photo, 
                    title: attachment.path
                }
            )
        }
    }

    if (!attachments || !attachments.length) return { cancel: true }

    const actions = [{
        name: 'addStagePhotos',
        data: {
            photos: arrPhotosPosted,
            stageId
        }
    }] as const;
    return {actions};
}

stages.addStagePhotos = addStagePhotos;

interface StageUpdate {
    metaData?: {
        identifier?: string,
        title?: string,
        description?: string,
        type?: string,
        internalBudget?: number,
        externalBudget?: number,
        ownerId?: string,
        estCompletionDate?: DateType
    },
    location?: {
        coordinates?: [number, number],
        properties?: {
            description?: string;
            city?: string;
            state?: string;
            zip?: string;
        }
    },
    tags?: string[]
}
export const updateStage: ActionCreator<['updateStage']> = (stageId: MStage['_id'], updateObject: StageUpdate) => {
    const actions = [{
        name: 'updateStage',
        data: {stageId, ...updateObject}
    }] as const;
    return {actions};
}

stages.updateStage = updateStage

