////// Packages //////////////////
import React, { FC, useRef, useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Badge, Button, Col, Media, Modal, ModalBody, ModalFooter, ModalHeader, Row, UncontrolledTooltip } from 'reactstrap';
import dayjs from 'dayjs';
import { IconProp } from '@fortawesome/fontawesome-svg-core';
import confirm from 'reactstrap-confirm';
import { toast } from 'react-toastify';

////// Local Components //////////
import TimeEntry from '../timesheet/TimeEntry';
import AddScheduleModal from '../timeoff/AddScheduleModal';

////// Global Components /////////
import Flex from 'components/Flex';
import FormGroupInput from 'components/FormGroupInput';

////// CommonJS Helpers //////////
import { FalconColors } from 'components/falconTypes';
import { pluralizeIf } from 'helpers/pluralizeIf';

////// Custom Hooks //////////////
import useAction from 'store/hooks/useAction';

////// Thunks ////////////////////
import {timeOffRequests, deleteTimeEntry, deleteTimeOffRequest} from 'store/thunks/time'
import { approveTimeOffRequest, reviewTimeEntry, timeEntries } from 'store/thunks/time';

/////// Types ////////////////////
import { MPerson } from 'store/types/models/id';
import { MTimeCode, MTimeEntry, MTimeOffRequest, MTimeType } from 'store/types/models/time';
import { useDispatch } from 'react-redux';

interface CircleStackIconProps {
  icon: IconProp;
  transform?: React.CSSProperties['transform']
}
const CircleStackIcon: FC<CircleStackIconProps> = ({icon, transform}) => (
  <span className="fa-stack ml-n1 mr-3">
    <FontAwesomeIcon icon="circle" className="text-200 fa-stack-2x" />
    <FontAwesomeIcon icon={icon} transform={transform ?? ''} className="text-primary fa-stack-1x" inverse />
  </span>
);

interface EventModalMediaContentProps {
  icon: IconProp;
  heading: string;
  content?: React.ReactNode;
  children?: React.ReactNode;
}
const EventModalMediaContent: FC<EventModalMediaContentProps> = ({ icon, heading, content, children }) => (
  <Media className="mt-3">
    <CircleStackIcon icon={icon} />
    <Media body>
      <>
        <h6>{heading}</h6>
        {children || <p className="mb-0 text-justify">{content}</p>}
      </>
    </Media>
  </Media>
);

export interface CalendarEventModalEvent {
  _id: MTimeEntry['_id'] | MTimeOffRequest['_id'];
  title: string;
  end: Date | null;
  start: Date | null;
  allDay?: boolean;
  description?: string;
  comments?: string; 
  stageIdentifier?: string;
  stageTitle?: string;
  milestoneTitle?: string;
  timeTypeId?: MTimeType['_id'];
  timeCodeId?: MTimeCode['_id'];
  taskId?: MTimeEntry['taskId'];
  stageId?: MTimeEntry['stageId'];
  taskTitle?: string;
  badge?: {
    color: FalconColors;
    label: string;
  },
  assignment?: {
    _id: string;
    description: string;
    dueDate: string;
    assignedHours: number;
    assignedBy: MPerson['_id'];
    assignerName: string;
  };
  approved?: boolean;
  declined?: boolean;
  declinedReason?: string;
  entryType: 'time' | 'timeOff'
}

interface Props {
  isOpen: boolean;
  setIsOpen: React.Dispatch<React.SetStateAction<boolean>>;
  event: CalendarEventModalEvent;
  personId: MPerson['_id']
}

const CalendarEventModal: FC<Props> = ({ isOpen, setIsOpen, event, personId }) => {
  const toggle = () => setIsOpen(!isOpen);
  const dispatch = useDispatch();
  const deleteTimeEntryAction = useAction( deleteTimeEntry );
  const deleteTimeOffRequestAction = useAction ( deleteTimeOffRequest )
  const respondTimeOffRequest = useAction(approveTimeOffRequest);
  const acceptTimeEntry = useAction( reviewTimeEntry )
  const [isOpenDeclineReasonModal, setIsOpenDeclineReasonModal] = useState(false);
  const [declineActionReason, setDeclineActionReason] = useState('');
  const [isOpenEditModal, setIsOpenEditModal] = useState(false);
  const declineResolver = useRef<(value: string | PromiseLike<string>) => void>()
  //dummy data!
  // const assignment = {
  //   assignedBy: 'Nicholas Dennspood',
  //   dueDate: '11/12/21',
  //   description: 'please complete all buttons and lil thangs on the user page. Also make sure to do your laundry and wipe your cheekz!',
  //   assignedHours: 45
  // }
  // const stageIdentifier = 'M17632'
  // const stageTitle = 'Develop Product'
  // const milestoneTitle='Finish user interface'
  // const taskTitle='Complete Admin pages'
  // const badge = {
  //   label: '6 hrs',
  //   color: 'soft-dark'
  // }
  // const approved = false;
  // const declined = true;
  // const declinedReason = 'you didnt even wipe your cheekz'

  const { 
    _id,
    title, 
    end, 
    start,
    allDay,
    description,
    comments,
    stageIdentifier,
    stageTitle,
    milestoneTitle,
    taskTitle, 
    badge,
    assignment,
    approved,
    declined,
    declinedReason,
    entryType
  } = event;

  const closeBtn = (
    <button className="close font-weight-normal" onClick={toggle}>
      &times;
    </button>
  );

  const handleDeclineRequest = async () => {
    const declinedReason = await new Promise<string>((res, rej)=>{
      setIsOpenDeclineReasonModal( true );
      declineResolver.current = res;
    })
    switch (entryType) {
      case 'time': {
        const data = await acceptTimeEntry.post(_id, false, declinedReason)
        if (data?.success) {
          setIsOpen(false);
          toast.success('Timesheet request declined')
        }
        if (!data?.success) toast.error(data?.error || 'something went wrong')
        break;
      } 
      case 'timeOff': {
        const data = await respondTimeOffRequest.post(_id, false, declinedReason)
        if (data?.success) {
          setIsOpen(false);
          toast.success('Time Off request declined')
        }
        if (!data?.success) toast.error(data?.error || 'something went wrong')
        break;
      }
    }
  }

  const handleAcceptRequest = async () => {
    switch (entryType) {
      case 'time': {
        const data = await acceptTimeEntry.post(_id, true)
        if (data?.success) {
          setIsOpen(false);
          toast.success('Timesheet request accepted')
        }
        if (!data?.success) toast.error(data?.error || 'something went wrong')
        break;
      }
      case 'timeOff': {
        const data = await respondTimeOffRequest.post(_id, true)
        if (data?.success) {
          setIsOpen(false);
          toast.success('Time off request accepted')
        }
        if (!data?.success) toast.error(data?.error || 'something went wrong')
        break;
      }
    }
  }

  const handleEditRequest = () => {
    setIsOpenEditModal(true)
  }

  const handleDeleteRequest = async () => {
    const shouldDelete = await confirm({
      message: 'Are you sure you want to delete this entry?',
      confirmColor: 'danger',
      confirmText: 'Delete',
      cancelColor: 'secondary',
      cancelText: 'Keep'
    })
    if ( shouldDelete ) {
      switch(entryType) {
        case 'time': {
            const data = await deleteTimeEntryAction.post(_id)
            if (data?.success) {
              dispatch( timeEntries.remove(_id) )
              toast.success('Deleted request')
              setIsOpen(false)
            } else {
              toast.error(data?.error || 'Something went wrong')
            }
            break;
        }
        case 'timeOff': {
          const data = await deleteTimeOffRequestAction.post(_id)
          if (data?.success) {
            dispatch( timeOffRequests.remove(_id) )
            toast.success('Deleted request')
            setIsOpen(false)
          } else {
            toast.error(data?.error || 'Something went wrong')
          }
          break;
        }
      }
    }
  }

  const canApprove = true;
  const canEdit = true;

  return (
    <Modal isOpen={isOpen} size="md" toggle={toggle} modalClassName="theme-modal" contentClassName="border" centered>
      <ModalHeader toggle={toggle} tag="div" className="px-card bg-light border-0 flex-between-center" close={closeBtn}>
        <h5 className="mb-1">{title}</h5>
        <Flex align="center">
          {badge && 
            <h5 className="mb-0">
              <Badge color={badge.color}>{badge.label}</Badge>
            </h5>
          }
          {approved 
              ? <h6 className="mb-0">
                  <FontAwesomeIcon className="text-success ml-3 mr-1" icon="check-circle" /> 
                  Approved
                </h6>
              : declined
                ? <h6 className="mb-0" style={{cursor: 'default'}} id='declined-request'>
                    <FontAwesomeIcon className="text-danger ml-3 mr-1" icon="times-circle" /> 
                    Declined
                    <UncontrolledTooltip placement='bottom' target='declined-request'>
                        {declinedReason}
                    </UncontrolledTooltip>
                  </h6>

                : <h6 className="mb-0">
                    <FontAwesomeIcon className="text-info ml-3 mr-1" icon="user-clock" /> 
                      Pending
                  </h6>
                  
            }
            </Flex>
      </ModalHeader>
      <ModalBody className="px-card pb-card pt-1 fs--1">
        { description && <EventModalMediaContent icon="align-left" heading="Description" content={description} /> }
        { comments && <EventModalMediaContent icon="align-left" heading="Comments" content={comments} /> }
        {(start) && (
          <EventModalMediaContent icon="calendar-check" heading="Time and Date">
            <p className="mb-2">
              {
                allDay 
                  ? (
                    (!end || dayjs(start).isSame(end, 'day'))
                      ? dayjs(start).format('MMMM D, YYYY')
                      : dayjs(start).isSame(end, 'year')
                        ? (
                          dayjs(start).isSame(end, 'month')
                            ? dayjs(start).format('MMMM D') + ' - ' + dayjs(end).format('D, YYYY')
                            : dayjs(start).format('MMMM D') + ' - ' + dayjs(end).format('MMMM D, YYYY')
                        )
                        : dayjs(start).format('MMMM D, YYYY') + ' - ' + dayjs(end).format('MMMM D, YYYY')
                  )
                  : (
                    end 
                      ? dayjs(start).isSame(end, 'day')
                        ? `${dayjs(start).format('MMMM D, YYYY h:mm A')} - ${dayjs(end).format('h:mm A')}`
                        : dayjs(start).format('MMMM D, YYYY h:mm A') + ' - ' + dayjs(end).format('MMMM D, YYYY h:mm A')
                      : `${dayjs(start).format('MMMM D, YYYY h:mm A')}`
                  )
              }
            </p>
          </EventModalMediaContent>
        )}
        <hr/>
        {(taskTitle || stageTitle) &&
          <>
            <Flex column justify="center">
              <h4 className='font-weight-bold my-1 mr-1 text-center'>
                <Badge color='soft-info'>
                  {stageIdentifier}: {stageTitle} 
                </Badge>
              </h4>
              <h4 className="text-center my-1 text-600"><FontAwesomeIcon icon='arrow-circle-down' /></h4>
              <h5 className='font-weight-bold my-1 mr-1 text-center'>
                <Badge color='soft-primary'>
                  {milestoneTitle}
                </Badge>
              </h5>
              <h5 className='text-center my-1 text-600'><FontAwesomeIcon icon='arrow-circle-down' /></h5>
              <h5 className='font-weight-bold my-1 mr-1 text-center fs-0'> 
                <Badge color='soft-success'>
                  {taskTitle} 
                </Badge>
              </h5>
            </Flex>
            
            <hr/>
          </>
        }
        {(assignment) && (
          <EventModalMediaContent icon="info" heading="Assignment Info">
              <Flex>
                <p className='font-weight-bold my-1 mr-1'>Assigned By: </p>
                <p className='my-1 ml-1'>{assignment?.assignerName}</p>
              </Flex>
              <Flex>
                <p className='font-weight-bold my-1 mr-1'>Due Date: </p>
                <p className='my-1 ml-1'>
                  {assignment && dayjs(assignment.dueDate).format("MM/DD/YYYY")} 
                  {(assignment.assignedHours) && `- ${assignment.assignedHours} estimated ${pluralizeIf('hour', assignment.assignedHours)}`}
                </p>
              </Flex>
              <Flex>
                <p className='font-weight-bold my-1 mr-1'>Description: </p>
                <p className='my-1 ml-1'>{assignment?.description}</p>
              </Flex>
          </EventModalMediaContent>
        )}
      </ModalBody>
      <ModalFooter className="px-card d-block bg-light border-top-0">
        <Row noGutters>
          {canEdit && 
            <Col xs='auto'>
              <UncontrolledTooltip placement='right' target='delete-time-entry-modal'>
                  Delete Time Entry
              </UncontrolledTooltip>
              <Button color='falcon-default' size='sm' id='delete-time-entry-modal' onClick={handleDeleteRequest}>
                <FontAwesomeIcon icon='trash-alt' className='text-danger'/>
              </Button>
            </Col>
          }
          
          {canApprove && !(approved || declined) &&
            <>
              <Col xs='auto' className='ml-auto mr-2'>
                <Button color='falcon-default' size='sm' onClick={handleAcceptRequest}>
                  <FontAwesomeIcon icon='check' className='mr-1 text-success'/>
                  Approve
                </Button>
              </Col>
              <Col xs='auto' className='mr-2'>
                <Button color='falcon-default' size='sm' onClick={handleDeclineRequest}>
                  <FontAwesomeIcon icon='times' className='mr-1 text-danger' />
                  Decline
                </Button>
                <Modal 
                  isOpen={isOpenDeclineReasonModal} 
                  toggle={()=>{
                    setIsOpenDeclineReasonModal(false)
                    setDeclineActionReason('')
                  }}
                >
                  <ModalHeader>
                    Decline Reason
                  </ModalHeader>
                  <ModalBody>
                    <FormGroupInput
                      id="decline-reason"
                      label="Enter Reason"
                      value={declineActionReason}
                      onChange={e=>setDeclineActionReason(e.target.value)}
                    />
                  </ModalBody>
                  <ModalFooter>
                    <Button color='primary' size='sm' onClick={()=>{
                      if (declineResolver.current) declineResolver.current(declineActionReason)
                      setIsOpenDeclineReasonModal(false)
                      setDeclineActionReason('')
                    }}>
                      Submit
                    </Button>
                  </ModalFooter>
                </Modal>
              </Col>
            </>
          }
          {canEdit && 
            <Col xs='auto' className={(approved || declined) ? 'ml-auto' : ''}>
              <Button tag="a" color="falcon-default" size="sm" onClick={handleEditRequest}>
                <FontAwesomeIcon icon="pencil-alt" className="fs--2 mr-2 text-primary" />
                <span>Edit</span>
              </Button>
              {entryType === 'time' && 
                <TimeEntry
                  event={{
                    ...event,
                    timeTypeId: event.timeTypeId!,
                    timeCodeId: event.timeCodeId!
                  }}
                  startDate={start?.toISOString()}
                  endDate={end?.toISOString()}
                  isOpen={isOpenEditModal}
                  setIsOpen={setIsOpenEditModal}
                  selectedPersonId={personId}
                />
              }
              {entryType === 'timeOff' && 
                <AddScheduleModal
                  event={{
                    ...event
                  }}
                  initialStartDate={start?.toISOString()}
                  initialEndDate={end?.toISOString()}
                  isOpen={isOpenEditModal}
                  setIsOpen={setIsOpenEditModal}
                />
              }
            </Col> 
          }
          
        </Row>
        
        
      </ModalFooter>
    </Modal>
  );
};

export default CalendarEventModal;
