////// Packages //////////////////
import React, { FC, useEffect, useRef, useState } from 'react'
import { Card, CardBody, CustomInput, Row, Col } from 'reactstrap';
import { useSelector } from 'react-redux';
import dayjs from 'dayjs';
import {  CalendarApi, EventContentArg, EventSourceInput } from '@fullcalendar/common';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { EventResizeDoneArg } from '@fullcalendar/interaction';
import { toast } from 'react-toastify';

////// Local Components //////////
import DisplayAccrual from '../components/DisplayAccrual';
import CalendarEventModal, { CalendarEventModalEvent } from '../components/CalendarEventModal';

////// Global Components /////////
import FalconCalendar from 'compositions/FalconCalendar';

import FalconCardHeader from 'components/FalconCardHeader';
import Flex from 'components/Flex';

////// CommonJS Helpers //////////
import TimeEntry, { DT_FORMAT } from './TimeEntry';
import { pluralizeIf } from 'helpers/pluralizeIf';
import { colorOptions } from 'components/falconTypes';

////// Custom Hooks //////////////
import useTimeCodes from 'store/hooks/time/useTimeCodes';
import useAction from 'store/hooks/useAction';
import useTimeEntries from 'store/hooks/time/useTimeEntries';
import useTimeOffRequests from 'store/hooks/time/useTimeOffRequests';

////// Thunks ////////////////////
import { updateTimeEntry, updateTimeOffRequest } from 'store/thunks/time';
import { MPerson } from 'store/types/models/id';

////// Types /////////////////////


interface Props {
    
}

const Timesheet: FC<Props> = () => {
    const apiRef = useRef<CalendarApi>();
    const updateTimeOffAction = useAction( updateTimeOffRequest );
    const updateTimeEntryAction = useAction( updateTimeEntry );
    const [eventModalEventData, setEventModalEventData] = useState<CalendarEventModalEvent>();

    
    const { value: timeCodes, ...timeCodesLoader } = useTimeCodes();
    const myPersonId = useSelector( state => state.app.login.personId );

    const [ startDate, setStartDate ] = useState( dayjs().startOf('week').toISOString() );
    const [ endDate, setEndDate ] = useState( dayjs().startOf('week').add(7, 'day').toISOString() );

    ///FORM STATE
    const [selectedPersonId, setSelectedPersonId] = useState<MPerson['_id']>();

    const [selectedStartDate, setSelectedStartDate] = useState('');
    const [selectedEndDate, setSelectedEndDate] = useState('');

    const [isOpen, setIsOpen] = useState(false);
    
    useEffect(()=> {
        if (myPersonId) 
            setSelectedPersonId(myPersonId)
    }, [myPersonId])

    const [showTimesheet, setShowTimesheet] = useState(true);
    const [showTimeOffRequests, setShowTimeOffRequests] = useState(true);
    const [showApprovedRequests, setShowApprovedRequests] = useState(true);
    const [showDeclinedRequests, setShowDeclinedRequests] = useState(true);
    const [showPendingRequests, setShowPendingRequests] = useState(true);

    const { value: timeEntries, ...timeEntriesLoader } = useTimeEntries(selectedPersonId || '', startDate, endDate)
    const {value: timeOffRequests, ...timeOffRequestLoader } = useTimeOffRequests(selectedPersonId, startDate, endDate)
    
    const allDayEvents: EventSourceInput = []

    timeEntries?.forEach( entry => {
        const evt = allDayEvents.findIndex( existing => dayjs(existing.start?.toString()).isSame(entry.startTime, 'day') );
        const entryHours = dayjs(entry.endTime).diff(entry.startTime, 'minute') / 60
        if (evt !== -1) {
            allDayEvents[evt].hours += entryHours
            allDayEvents[evt].classNames = [allDayEvents[evt].hours < 8 ? "bg-500 text-800" : allDayEvents[evt].hours === 8 ? "badge-success" : "badge-warning"]
            allDayEvents[evt].title = `Total hours: ${allDayEvents[evt].hours}`
        } else {
            allDayEvents.push({
                id: dayjs(entry.startTime).format("MMDDYY"),
                start: entry.startTime,
                allDay: true,
                selectable: false,
                hours: entryHours,
                editable: false,
                classNames: [entryHours === 8 ? "badge-success" : "badge-secondary"],
                title: `Total hours: ${entryHours}`
            })
        }
    })

    const usedTimeEntries = showTimesheet 
                                ? (timeEntries?.map(e => (
                                        {
                                            ...e, 
                                            start: e.startTime, 
                                            end: e.endTime, 
                                            title: e.taskTitle,
                                            entryType: 'time',
                                            approved: !!e.approverId,
                                            declined: !!e.declinerId
                                        }
                                    )) || [])
                                :[]

    const usedTimeOffRequests = showTimeOffRequests
                                    ? (timeOffRequests?.map(e=>(
                                            {
                                                ...e, 
                                                start: e.startDate, 
                                                end: e.endDate || (e.allDay && e.startDate),
                                                className: e.approved ? "bg-stripes-info"
                                                            : e.declined ? "bg-stripes-danger"
                                                            :"bg-stripes-warning",
                                                entryType: 'timeOff',
                                                hours: e.absentHours,
                                                display: 'background'
                                            }
                                        )) || [])
                                    : []

    const combinedEvents = [
        ...usedTimeEntries, 
        ...usedTimeOffRequests
    ].filter( e => 
        (showApprovedRequests && e.approved) 
        || (showDeclinedRequests && e.declined) 
        || ( showPendingRequests && (!e.approved && !e.declined) ) 
    )

    const rawEvents: EventSourceInput = combinedEvents?.map( timeEntry => {
        const color = timeCodes?.find( timeType => timeType.timeCodes.find( timeCode => timeEntry.timeCode._id === timeCode._id ) )?.eventColor
        return {
            ...timeEntry,
            id: timeEntry._id,
            title: `${timeEntry.timeCode.title}: ${timeEntry.title || ''}`,
            textColor: 'inherit',
            badge: timeEntry.hours ? {
                label: timeEntry.hours + ' ' + pluralizeIf('hr', timeEntry.hours),
                color: color || 'soft-dark'
            }: undefined,
            classNames: color ? [`bg-${color}`, `text-${colorOptions.find( option => option.value === color )?.text}`, 'overflow-hidden'] : ['overflow-hidden']
        }
    }) || []

    const events = [...rawEvents, ...allDayEvents];



    function formatTimeEntryTitle(timeEntry: EventContentArg) {
        if (timeEntry.event.allDay) return timeEntry.event.title
        const originalEntry = timeEntry.event.extendedProps;
        const taskTitle = originalEntry?.taskTitle; // Program Timesheet Interface --- pri 1
        const timeCode = originalEntry?.timeCode?.title; // Development Projects -- pri 2  ?
        const hours = (timeEntry.event.end && timeEntry.event.start) ? dayjs(timeEntry.event.end?.toISOString()).diff(timeEntry.event.start?.toISOString(), 'minute') / 60 : 0
        let fixedPlaces = 0;
        if (hours && hours % 1) {
            fixedPlaces = 1
        }
        const hoursDisplay = hours ? hours.toFixed(fixedPlaces) : 'unknown'
        const duration = hoursDisplay + ' hr' + (hours === 1 ? '' : 's'); // 
        return (
            <div className="text-truncate w-100">
                <Flex justify="between">
                    <h6 className="mb-0">{`${timeEntry.timeText} (${duration})`}</h6>
                    {originalEntry.approved && <FontAwesomeIcon icon="check-circle" className="mr-1" />}
                    {originalEntry.declined && <FontAwesomeIcon icon="times-circle" className="mr-1" />}
                </Flex>
                <p className="my-1 font-weight-bold"><i>{timeCode}</i></p>
                {taskTitle && <p className="my-1 fs--1"><i>{taskTitle}</i></p>}
            </div>
        )
    }
    




    async function handleEventChange({event, revert}: EventResizeDoneArg) {
        if (event.extendedProps.entryType === "time") {
            const data = await updateTimeEntryAction.post(event.id, {startTime: event.start?.toISOString(), endTime: event.end?.toISOString()})
            if (!data?.success) {
                toast.error(data?.error || "something went wrong")
                revert()
            }
        } else if (event.extendedProps.entryType === "timeOff") {
            const data = await updateTimeOffAction.post(event.id, {startDate: event.start?.toISOString(), endDate: event.end?.toISOString()})
            if (!data?.success) {
                toast.error(data?.error || "something went wrong")
                revert()
            }
        }
        
    }


    return (
        <>
            
            <Row>
                <Col xl={10} className="order-1 order-xl-0">
                    <FalconCalendar
                        apiRef={apiRef}
                        editable 
                        selectable 
                        onAddEventClick={()=>setIsOpen(true)} 
                        addEventText="Add Time Entry"
                        select={info=>{
                            console.log(info)
                            if (info.view.type === "dayGridMonth") {
                                const s = info.start.getTime()
                                const e = info.end.getTime()
                                if ( e - s !== (1000 * 3600 * 24) ) {
                                   apiRef.current?.changeView('timeGridWeek', info.startStr)
                                } else {
                                    apiRef.current?.changeView('timeGridDay', info.start)
                                }
                            } else {
                                setIsOpen(true);
                                setSelectedStartDate(dayjs(info.start).format(DT_FORMAT))
                                setSelectedEndDate(dayjs(info.end).format(DT_FORMAT))
                            }
                            
                        }}
                        eventClick={ arg => {
                            setEventModalEventData({
                                ...(arg.event.extendedProps as any),
                                timeCodeId: arg.event.extendedProps?.timeCode?._id,
                                title: arg.event.title,
                                start: arg.event.start,
                                end: arg.event.end,
                                allDay: arg.event.allDay
                            })
                        } }
                        eventResize={ args => {handleEventChange(args); return true} }
                        eventDrop={ args => {handleEventChange(args as any); return true} }
                        events={events}
                        initialView={window.innerWidth < 750 ? "timeGridDay" : "timeGridWeek"}
                        allDaySlot
                        allDayContent="Total Hours"
                        nowIndicator
                        eventContent={ item => formatTimeEntryTitle(item)}
                        height="auto"
                        dayMaxEvents={4}
                        datesSet={ info => {
                            setStartDate(info.start.toISOString())
                            setEndDate(info.end.toISOString())
                        }}
                    />
                </Col>
                <Col xl={2} className="order-0 order-xl-1">
                    <Row>
                        <Col xs={12} lg="auto" xl={12} className="d-block d-lg-flex d-xl-block">
                            <Card className="mb-3">
                                <FalconCardHeader title='Filters' />
                                <CardBody>
                                    <Row>
                                        <Col xs={6} xl={12}>
                                            <CustomInput
                                                id="show-timesheets"
                                                label="Timesheet"
                                                onChange={() => setShowTimesheet(!showTimesheet)}
                                                checked={showTimesheet}
                                                type="switch"
                                            />
                                            <CustomInput
                                                id="show-timeOff"
                                                className="mt-3"
                                                label="Time Off"
                                                onChange={() => setShowTimeOffRequests(!showTimeOffRequests)}
                                                checked={showTimeOffRequests}
                                                type="switch"
                                            />
                                        </Col>
                                        <Col xs={6} xl={12}>
                                            <hr className="d-none d-xl-block" />
                                            <h6 className="d-none d-xl-block" >Requests:</h6>
                                            <CustomInput
                                                id="show-approved-requests"
                                                className="mt-xl-3"
                                                label="Approved"
                                                onChange={() => setShowApprovedRequests(!showApprovedRequests)}
                                                checked={showApprovedRequests}
                                                type="switch"
                                            />
                                            <CustomInput
                                                id="show-declined-requests"
                                                className="mt-3"
                                                label="Declined"
                                                onChange={() => setShowDeclinedRequests(!showDeclinedRequests)}
                                                checked={showDeclinedRequests}
                                                type="switch"
                                            />
                                            <CustomInput
                                                id="show-pending-requests"
                                                className="mt-3"
                                                label="Pending"
                                                onChange={() => setShowPendingRequests(!showPendingRequests)}
                                                checked={showPendingRequests}
                                                type="switch"
                                            />
                                        </Col>
                                    </Row>
                                    
                                </CardBody>
                            </Card>
                        </Col>
                        <Col>
                            {selectedPersonId && <DisplayAccrual personId={selectedPersonId}  className="mb-3" />}    
                        </Col> 
                    </Row>
                </Col>
            </Row>
           <TimeEntry
                startDate={selectedStartDate}
                endDate={selectedEndDate}
                setIsOpen={setIsOpen}
                isOpen={isOpen}
                selectedPersonId={selectedPersonId}
           />
            
            {
                eventModalEventData && selectedPersonId &&
                    <CalendarEventModal
                        isOpen={!!eventModalEventData}
                        setIsOpen={()=>setEventModalEventData(undefined)}
                        event={eventModalEventData}
                        personId={selectedPersonId}
                    />
            }
        </>
    )
}

export default Timesheet;

