////// Packages //////////////////
import React, { FC, useEffect, useRef, useState } from 'react';
import { Button, Card, CardBody, CardHeader, Col, Row, UncontrolledTooltip } from 'reactstrap';
import FullCalendar, { CalendarApi, CalendarOptions } from '@fullcalendar/react';
import dayGridPlugin from '@fullcalendar/daygrid';
import bootstrapPlugin from '@fullcalendar/bootstrap';
import timeGridPlugin from '@fullcalendar/timegrid';
import listPlugin from '@fullcalendar/list';
import interactionPlugin from '@fullcalendar/interaction';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

////// Local Components //////////

////// Global Components /////////
import DropdownFilter from 'components/DropdownFilter';
import Flex from 'components/Flex';
import { isIterableArray } from 'helpers/utils';
import dayjs from 'dayjs';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////
 interface Props extends Omit<CalendarOptions, 'plugins' | 'ref' | 'headerToolbar' | 'themeSystem' | 'selectMirror'> {
   apiRef?: React.MutableRefObject<CalendarApi | undefined>;
   onAddEventClick?: ()=>void;
   addEventText?: string;
 }

 const DEFAULT_EVENT_TIME_FORMAT = {
  hour: 'numeric',
  minute: '2-digit',
  omitZeroMinute: true,
  meridiem: true
} as const;

const DEFAULT_BUTTON_TEXT = {
  today: 'Today',
  month: 'Month view',
  week: 'week',
  day: 'day',
  listWeek: 'list view',
  listYear: 'year'
};

type ViewName = 'Month View' | 'Week View' | 'Day View' | 'List View' | 'Year View';
type ViewOption = 'dayGridMonth' | 'timeGridWeek' | 'timeGridDay' | 'listWeek' | 'listYear'

const views: [ViewName, ViewOption][] = [
  ['Month View', 'dayGridMonth'],
  ['Week View', 'timeGridWeek'],
  ['Day View', 'timeGridDay'],
  ['List View', 'listWeek'],
  ['Year View', 'listYear']
]

const FalconCalendar: FC<Props> = ({
  apiRef,
  onAddEventClick,
  addEventText="Add Event",
  initialView="dayGridMonth", 
  dayMaxEvents=2, 
  height=800, 
  stickyHeaderDates=false, 
  eventTimeFormat=DEFAULT_EVENT_TIME_FORMAT, 
  buttonText=DEFAULT_BUTTON_TEXT,
  events,
  ...calendarProps
}) => {
  const calendarRef = useRef<FullCalendar>(null);
  const [calendarApi, setCalendarApi] = useState(undefined as CalendarApi | undefined);
  const [title, setTitle] = useState('');
  const [currentFilter, setCurrentFilter] = useState(views.find( view => view[1] === initialView)?.[0] || 'Month View' as ViewName);
  const [viewAllTime, setViewAllTime] = useState(false);

  useEffect(() => {
    setCalendarApi(calendarRef.current?.getApi());
    if (apiRef && calendarRef?.current) apiRef.current = calendarRef.current.getApi()
  }, []);

  useEffect(() => {
    const t = apiRef?.current?.getCurrentData().viewApi.type;
    if (t) {
      const newView = views.find( view => view[1] === t )?.[0]
      if (newView) 
        handleFilter( newView )
    }
  }, [apiRef?.current?.getCurrentData().viewApi.type])

  const handleFilter = (filter: ViewName) => {
    setCurrentFilter(filter);
    const newView = views.find( view => view[0] === filter)?.[1]
    if (newView) calendarApi?.changeView(newView);
    setTitle(calendarApi?.getCurrentData().viewTitle || '');
  };

  const minHour = Math.min(dayjs().hour(), 7)
  const maxHour = Math.max(dayjs().hour(), 18)

  const countOutOfRange = isIterableArray(events) && events.reduce( (total, event) => {
      if(!(event.start && event.end)) return total
      const start = dayjs(event.start)
      const end = dayjs(event.end)
      if (start.add(1, 'day').isBefore(end)) return total
      if (start.hour() > minHour && start.hour() < maxHour) return total
      if (end.hour() > minHour && end.hour() < maxHour) return total
      return total + 1
  }, 0)
  
  const viewsConfig = {
      week: {
        eventLimit: 5,
        slotMinTime: viewAllTime ? "00:00:00" : dayjs().hour(minHour).minute(0).format('HH:mm:ss'),
        slotMaxTime: viewAllTime ? "24:00:00" : dayjs().hour(maxHour).minute(60).format('HH:mm:ss')
      },
      day: {
        eventLimit: 5,
        slotMinTime: viewAllTime ? "00:00:00" : dayjs().hour(minHour).minute(0).format('HH:mm:ss'),
        slotMaxTime: viewAllTime ? "24:00:00" : dayjs().hour(maxHour).minute(60).format('HH:mm:ss')
      }
    }

  return (
    <>
      <Card className="mb-3">
        <CardHeader>
          <Row noGutters className="align-items-center">
            <Col xs="auto" className="d-flex justify-content-end order-md-1">
              <UncontrolledTooltip placement="bottom" target="previous">
                Previous
              </UncontrolledTooltip>
              <UncontrolledTooltip placement="bottom" target="next">
                Next
              </UncontrolledTooltip>
              <Button
                onClick={() => {
                  calendarApi?.prev();
                  setTitle(calendarApi?.getCurrentData().viewTitle || '');
                }}
                color="link"
                className="icon-item icon-item-sm icon-item-hover shadow-none p-0 mr-1 ml-md-2"
                id="previous"
              >
                <FontAwesomeIcon icon="arrow-left" />
              </Button>
              <Button
                onClick={() => {
                  calendarApi?.next();
                  setTitle(calendarApi?.getCurrentData().viewTitle || '');
                }}
                color="link"
                className="icon-item icon-item-sm icon-item-hover shadow-none p-0 mr-1"
                id="next"
              >
                <FontAwesomeIcon icon="arrow-right" />
              </Button>
            </Col>
            <Col xs="auto" md="auto" className="order-md-2">
              <h4 className="mb-0 fs-0 fs-sm-1 fs-lg-2 calendar-title">
                {title || `${calendarApi?.getCurrentData().viewTitle}`}
              </h4>
            </Col>
            <Col xs md="auto" tag={Flex} justify="end" className="order-md-3">
              <Button
                size="sm"
                color="falcon-primary"
                onClick={() => {
                  calendarApi?.today();
                  setTitle(calendarApi?.getCurrentData().viewTitle || '');
                }}
              >
                Today
              </Button>
            </Col>
            <Col md="auto" className="d-md-none">
              <hr />
            </Col>
            <Col xs="auto" className="d-flex order-md-0">
              {
                onAddEventClick && 
                <Button color="primary" size="sm" onClick={onAddEventClick}>
                  <FontAwesomeIcon icon="plus" className="mr-1" /> {addEventText}
                </Button>
              }
            </Col>
            <Col className="d-flex justify-content-end order-md-2">
              <DropdownFilter
                className="mr-2"
                filters={views.map(view=>view[0])}
                currentFilter={currentFilter}
                handleFilter={handleFilter}
                icon="sort"
                right
              />
            </Col>
          </Row>
        </CardHeader>
        <CardBody className="p-0">
          <FullCalendar
            ref={calendarRef}
            headerToolbar={false}
            plugins={[dayGridPlugin, bootstrapPlugin, timeGridPlugin, interactionPlugin, listPlugin]}
            initialView={initialView}
            themeSystem="bootstrap"
            dayMaxEvents={dayMaxEvents}
            height={height}
            stickyHeaderDates={stickyHeaderDates}
            selectMirror
            eventTimeFormat={eventTimeFormat}
            buttonText={buttonText}
            events={events}
            views={viewsConfig}
            {...calendarProps}
          />
          {
            (currentFilter === 'Day View' || currentFilter === 'Week View') && 
            <>
              {
                !!countOutOfRange && !viewAllTime &&
                  <Flex align="center" justify="center">
                    <h5 className="my-2">{`+ ${countOutOfRange} hidden`}</h5>
                  </Flex>
              }
              <Flex column>
                <Button color="falcon-primary" id="calendar-expand" onClick={()=>setViewAllTime(v=>!v)}>
                  <FontAwesomeIcon icon="angle-down"  transform={viewAllTime ? "rotate-180" : ''}/>
                </Button>
                <UncontrolledTooltip target="calendar-expand">{viewAllTime ? "Collapse Time" : "Show All Time"}</UncontrolledTooltip>
              </Flex>
            </>
          }
        </CardBody>
      </Card>
    </>
  );
};

export default FalconCalendar;
